import React, { useState, useMemo } from 'react';
import { useUIStore } from '../../stores/uiStore';
import { useAppStore, useCurrentConfig } from '../../store/index';
import { useValidation } from '../../hooks/useValidation';
import { Button } from '../ui/Button';
import { Card } from '../ui/Card'; 
import { Input } from '../ui/Input';
import { Plus, Settings, BarChart3, Trash2, Copy, Edit3, Check, X } from 'lucide-react';
import { ChartsPanel } from '../tools/ChartsPanel';
import type { SamplePair } from '../../types/index';

export const RightSidebar: React.FC = () => {
  const { toggleRightSidebar } = useUIStore();
  const { updateConfig } = useAppStore();
  const currentConfig = useCurrentConfig();
  
  const [activeTab, setActiveTab] = useState<'pairs' | 'settings' | 'visualization'>('pairs');
  const [showAddPair, setShowAddPair] = useState(false);
  const [editingPairId, setEditingPairId] = useState<string | null>(null);
  const [newPairName, setNewPairName] = useState('');
  const [newPair, setNewPair] = useState({
    population1: { mean: 100, std: 15, size: 1000 },
    population2: { mean: 110, std: 15, size: 1000 },
    sampleSize: 30
  });

  // Editing state for existing pairs
  const [editingPair, setEditingPair] = useState<SamplePair | null>(null);
  const [editingPairName, setEditingPairName] = useState('');

  // Initialize validation hook
  const {
    validateNumberField,
    validateStringField,
    getFieldError,
    getFieldWarning,
    calculateEffectSizeWithValidation,
    clearValidation,
    validationState
  } = useValidation({ debounceMs: 300 });

  const handleAddSamplePair = () => {
    if (!currentConfig || !newPairName.trim()) return;
    
    // Validate the complete sample pair before adding
    const isNameValid = validateStringField(newPairName, 'newPair.name', { 
      required: true, 
      maxLength: 50 
    });

    const isPop1Valid = 
      validateNumberField(newPair.population1.mean, 'newPair.population1.mean', { finite: true }) &&
      validateNumberField(newPair.population1.std, 'newPair.population1.std', { positive: true }) &&
      validateNumberField(newPair.population1.size, 'newPair.population1.size', { 
        integer: true, 
        min: 10, 
        max: 10000 
      });

    const isPop2Valid = 
      validateNumberField(newPair.population2.mean, 'newPair.population2.mean', { finite: true }) &&
      validateNumberField(newPair.population2.std, 'newPair.population2.std', { positive: true }) &&
      validateNumberField(newPair.population2.size, 'newPair.population2.size', { 
        integer: true, 
        min: 10, 
        max: 10000 
      });

    const isSampleSizeValid = validateNumberField(newPair.sampleSize, 'newPair.sampleSize', {
      integer: true,
      min: 5,
      max: 1000
    });

    if (!isNameValid || !isPop1Valid || !isPop2Valid || !isSampleSizeValid) {
      return; // Don't add if validation fails
    }

    const samplePair: SamplePair = {
      id: `pair-${Date.now()}`,
      name: newPairName,
      population1: newPair.population1,
      population2: newPair.population2,
      sampleSize: newPair.sampleSize
    };

    updateConfig({
      samplePairs: [...currentConfig.samplePairs, samplePair],
      updatedAt: new Date()
    });

    // Reset form and clear validation
    setNewPairName('');
    setNewPair({
      population1: { mean: 100, std: 15, size: 1000 },
      population2: { mean: 110, std: 15, size: 1000 },
      sampleSize: 30
    });
    setShowAddPair(false);
    clearValidation();
  };

  const handleRemovePair = (pairId: string) => {
    if (!currentConfig) return;
    
    updateConfig({
      samplePairs: currentConfig.samplePairs.filter(p => p.id !== pairId),
      updatedAt: new Date()
    });
  };

  const handleDuplicatePair = (pair: SamplePair) => {
    if (!currentConfig) return;
    
    const duplicatedPair: SamplePair = {
      id: `pair-${Date.now()}`,
      name: `${pair.name} (Copy)`,
      population1: { ...pair.population1 },
      population2: { ...pair.population2 },
      sampleSize: pair.sampleSize
    };

    updateConfig({
      samplePairs: [...currentConfig.samplePairs, duplicatedPair],
      updatedAt: new Date()
    });
  };

  const handleStartEdit = (pair: SamplePair) => {
    setEditingPairId(pair.id);
    setEditingPair({ ...pair });
    setEditingPairName(pair.name);
    clearValidation();
  };

  const handleCancelEdit = () => {
    setEditingPairId(null);
    setEditingPair(null);
    setEditingPairName('');
    clearValidation();
  };

  const handleSaveEdit = () => {
    if (!currentConfig || !editingPair || !editingPairName.trim()) return;

    // Validate the edited pair
    const isNameValid = validateStringField(editingPairName, 'editPair.name', { 
      required: true, 
      maxLength: 50 
    });

    const isPop1Valid = 
      validateNumberField(editingPair.population1.mean, 'editPair.population1.mean', { finite: true }) &&
      validateNumberField(editingPair.population1.std, 'editPair.population1.std', { positive: true }) &&
      validateNumberField(editingPair.population1.size, 'editPair.population1.size', { 
        integer: true, 
        min: 10, 
        max: 10000 
      });

    const isPop2Valid = 
      validateNumberField(editingPair.population2.mean, 'editPair.population2.mean', { finite: true }) &&
      validateNumberField(editingPair.population2.std, 'editPair.population2.std', { positive: true }) &&
      validateNumberField(editingPair.population2.size, 'editPair.population2.size', { 
        integer: true, 
        min: 10, 
        max: 10000 
      });

    const isSampleSizeValid = validateNumberField(editingPair.sampleSize, 'editPair.sampleSize', {
      integer: true,
      min: 5,
      max: 1000
    });

    if (!isNameValid || !isPop1Valid || !isPop2Valid || !isSampleSizeValid) {
      return; // Don't save if validation fails
    }

    const updatedPairs = currentConfig.samplePairs.map(pair => 
      pair.id === editingPair.id 
        ? { ...editingPair, name: editingPairName }
        : pair
    );

    updateConfig({
      samplePairs: updatedPairs,
      updatedAt: new Date()
    });

    handleCancelEdit();
  };

  // Handle input changes with validation
  const handlePairNameChange = (value: string) => {
    setNewPairName(value);
    validateStringField(value, 'newPair.name', { required: true, maxLength: 50 });
  };

  const handlePopulation1MeanChange = (value: string) => {
    const numValue = parseFloat(value) || 0;
    setNewPair(prev => ({
      ...prev,
      population1: { ...prev.population1, mean: numValue }
    }));
    validateNumberField(numValue, 'newPair.population1.mean', { finite: true });
  };

  const handlePopulation1StdChange = (value: string) => {
    const numValue = parseFloat(value) || 1;
    setNewPair(prev => ({
      ...prev,
      population1: { ...prev.population1, std: numValue }
    }));
    validateNumberField(numValue, 'newPair.population1.std', { positive: true });
  };

  const handlePopulation2MeanChange = (value: string) => {
    const numValue = parseFloat(value) || 0;
    setNewPair(prev => ({
      ...prev,
      population2: { ...prev.population2, mean: numValue }
    }));
    validateNumberField(numValue, 'newPair.population2.mean', { finite: true });
  };

  const handlePopulation2StdChange = (value: string) => {
    const numValue = parseFloat(value) || 1;
    setNewPair(prev => ({
      ...prev,
      population2: { ...prev.population2, std: numValue }
    }));
    validateNumberField(numValue, 'newPair.population2.std', { positive: true });
  };

  const handleSampleSizeChange = (value: string) => {
    const numValue = parseInt(value) || 30;
    setNewPair(prev => ({
      ...prev,
      sampleSize: numValue
    }));
    validateNumberField(numValue, 'newPair.sampleSize', {
      integer: true,
      min: 5,
      max: 1000
    });
  };

  // Handle input changes for editing existing pairs
  const handleEditPairNameChange = (value: string) => {
    setEditingPairName(value);
    validateStringField(value, 'editPair.name', { required: true, maxLength: 50 });
  };

  const handleEditPopulation1MeanChange = (value: string) => {
    if (!editingPair) return;
    const numValue = parseFloat(value) || 0;
    setEditingPair(prev => ({
      ...prev!,
      population1: { ...prev!.population1, mean: numValue }
    }));
    validateNumberField(numValue, 'editPair.population1.mean', { finite: true });
  };

  const handleEditPopulation1StdChange = (value: string) => {
    if (!editingPair) return;
    const numValue = parseFloat(value) || 1;
    setEditingPair(prev => ({
      ...prev!,
      population1: { ...prev!.population1, std: numValue }
    }));
    validateNumberField(numValue, 'editPair.population1.std', { positive: true });
  };

  const handleEditPopulation2MeanChange = (value: string) => {
    if (!editingPair) return;
    const numValue = parseFloat(value) || 0;
    setEditingPair(prev => ({
      ...prev!,
      population2: { ...prev!.population2, mean: numValue }
    }));
    validateNumberField(numValue, 'editPair.population2.mean', { finite: true });
  };

  const handleEditPopulation2StdChange = (value: string) => {
    if (!editingPair) return;
    const numValue = parseFloat(value) || 1;
    setEditingPair(prev => ({
      ...prev!,
      population2: { ...prev!.population2, std: numValue }
    }));
    validateNumberField(numValue, 'editPair.population2.std', { positive: true });
  };

  const handleEditSampleSizeChange = (value: string) => {
    if (!editingPair) return;
    const numValue = parseInt(value) || 30;
    setEditingPair(prev => ({
      ...prev!,
      sampleSize: numValue
    }));
    validateNumberField(numValue, 'editPair.sampleSize', {
      integer: true,
      min: 5,
      max: 1000
    });
  };

  // Calculate current effect size for display
  const currentEffectSize = useMemo(() => {
    return calculateEffectSizeWithValidation(
      newPair.population1.mean,
      newPair.population2.mean,
      newPair.population1.std,
      newPair.population2.std,
      'newPair'
    );
  }, [
    newPair.population1.mean,
    newPair.population2.mean,
    newPair.population1.std,
    newPair.population2.std,
    calculateEffectSizeWithValidation
  ]);

  const renderSamplePairsTab = () => {
    if (!currentConfig) {
      return (
        <div className="p-3 text-center text-muted-foreground">
          <BarChart3 className="h-6 w-6 mx-auto mb-2 opacity-50" />
          <p className="text-xs">No active simulation</p>
          <p className="text-[10px] mt-1">Create or load a simulation to configure sample pairs</p>
        </div>
      );
    }

    return (
      <div className="p-2 space-y-2">
        <div className="space-y-2">
          <div className="flex items-center justify-between">
            <h3 className="text-xs text-sidebar-foreground">
              Sample Pairs ({currentConfig.samplePairs.length})
            </h3>
            <Button
              size="sm"
              onClick={() => setShowAddPair(true)}
              disabled={showAddPair || editingPairId !== null}
              className="text-[10px] px-1.5 py-0.5"
            >
              <Plus className="h-2.5 w-2.5 mr-1" />
              Add
            </Button>
          </div>

          {currentConfig.samplePairs.length === 0 && (
            <div className="text-center py-4 text-muted-foreground">
              <BarChart3 className="h-8 w-8 mx-auto mb-2 opacity-30" />
              <p className="text-xs">No sample pairs configured</p>
              <p className="text-[10px] mt-1">Add your first sample pair to get started</p>
            </div>
          )}

          {currentConfig.samplePairs.map((pair) => {
            const isEditing = editingPairId === pair.id;
            const currentPair = isEditing ? editingPair! : pair;
            // const currentName = isEditing ? editingPairName : pair.name; // Unused
            
            const effectSize = (currentPair.population2.mean - currentPair.population1.mean) / 
              Math.sqrt((currentPair.population1.std ** 2 + currentPair.population2.std ** 2) / 2);

        return (
              <Card key={pair.id} className="p-2 bg-sidebar-accent border-border">
                {isEditing ? (
                  // Edit Mode
                  <div className="space-y-2">
                    <div className="flex items-center justify-between mb-2">
                      <h4 className="text-xs text-sidebar-foreground">Editing Sample Pair</h4>
                      <div className="flex gap-1">
                        <button
                          onClick={handleSaveEdit}
                          disabled={validationState.hasErrors || !editingPairName.trim()}
                          className="p-1 hover:bg-green-700 rounded text-green-400 hover:text-green-300 disabled:opacity-50 disabled:cursor-not-allowed"
                          title="Save changes"
                        >
                          <Check className="h-3 w-3" />
                        </button>
                        <button
                          onClick={handleCancelEdit}
                          className="p-1 hover:bg-zinc-700 rounded text-zinc-400 hover:text-zinc-300"
                          title="Cancel editing"
                        >
                          <X className="h-3 w-3" />
                        </button>
                      </div>
                    </div>

                    <Input
                      label="Pair Name"
                      value={editingPairName}
                      onChange={(e) => handleEditPairNameChange(e.target.value)}
                      className="text-xs"
                      error={getFieldError('editPair.name')}
                    />

                    <div className="grid grid-cols-2 gap-3">
                      <div className="space-y-2">
                        <label className="text-xs text-zinc-400">Population 1</label>
                        <Input
                          label="Mean"
                          type="number"
                          value={currentPair.population1.mean}
                          onChange={(e) => handleEditPopulation1MeanChange(e.target.value)}
                          className="text-xs"
                          error={getFieldError('editPair.population1.mean')}
                        />
                        <Input
                          label="Std Dev"
                          type="number"
                          value={currentPair.population1.std}
                          onChange={(e) => handleEditPopulation1StdChange(e.target.value)}
                          className="text-xs"
                          error={getFieldError('editPair.population1.std')}
                        />
                      </div>
                      
                      <div className="space-y-2">
                        <label className="text-xs text-zinc-400">Population 2</label>
                        <Input
                          label="Mean"
                          type="number"
                          value={currentPair.population2.mean}
                          onChange={(e) => handleEditPopulation2MeanChange(e.target.value)}
                          className="text-xs"
                          error={getFieldError('editPair.population2.mean')}
                        />
                        <Input
                          label="Std Dev"
                          type="number"
                          value={currentPair.population2.std}
                          onChange={(e) => handleEditPopulation2StdChange(e.target.value)}
                          className="text-xs"
                          error={getFieldError('editPair.population2.std')}
                        />
                      </div>
                    </div>
                    
                    <Input
                      label="Sample Size (n)"
                      type="number"
                      value={currentPair.sampleSize}
                      onChange={(e) => handleEditSampleSizeChange(e.target.value)}
                      className="text-xs"
                      error={getFieldError('editPair.sampleSize')}
                    />

                    {/* Effect Size Display */}
                    <div className="bg-sidebar p-2 rounded border border-border">
                      <div className="flex items-center justify-between">
                        <span className="text-[10px] text-muted-foreground">Effect Size (Cohen's d):</span>
                        <span className={`text-xs ${
                          Math.abs(effectSize) < 0.2 ? 'text-muted-foreground' :
                          Math.abs(effectSize) < 0.5 ? 'text-sidebar-foreground' :
                          Math.abs(effectSize) < 0.8 ? 'text-sidebar-foreground' :
                          'text-sidebar-foreground'
                        }`}>
                          {effectSize.toFixed(3)}
                        </span>
                      </div>
                    </div>
                  </div>
                ) : (
                  // View Mode
                  <>
                    <div className="flex items-start justify-between mb-2">
                      <div className="min-w-0 flex-1">
                        <h4 className="text-xs text-sidebar-foreground truncate">{pair.name}</h4>
                        <p className="text-[10px] text-muted-foreground">Sample size: n = {pair.sampleSize}</p>
                      </div>
                      <div className="flex gap-1">
                        <button
                          onClick={() => handleStartEdit(pair)}
                          disabled={showAddPair || editingPairId !== null}
                          className="p-0.5 hover:bg-sidebar rounded text-muted-foreground hover:text-sidebar-foreground disabled:opacity-50 disabled:cursor-not-allowed"
                          title="Edit pair"
                        >
                          <Edit3 className="h-2.5 w-2.5" />
                        </button>
                        <button
                          onClick={() => handleDuplicatePair(pair)}
                          disabled={showAddPair || editingPairId !== null}
                          className="p-0.5 hover:bg-sidebar rounded text-muted-foreground hover:text-sidebar-foreground disabled:opacity-50 disabled:cursor-not-allowed"
                          title="Duplicate pair"
                        >
                          <Copy className="h-2.5 w-2.5" />
                        </button>
                        <button
                          onClick={() => handleRemovePair(pair.id)}
                          disabled={showAddPair || editingPairId !== null}
                          className="p-0.5 hover:bg-sidebar rounded text-muted-foreground hover:text-destructive disabled:opacity-50 disabled:cursor-not-allowed"
                          title="Remove pair"
                        >
                          <Trash2 className="h-2.5 w-2.5" />
                        </button>
                      </div>
                    </div>

                    <div className="space-y-1 text-[10px]">
                      <div className="grid grid-cols-2 gap-1">
                        <div className="bg-sidebar p-1.5 rounded border border-border">
                          <div className="text-muted-foreground mb-0.5">Population 1</div>
                          <div className="text-sidebar-foreground">μ = {pair.population1.mean}</div>
                          <div className="text-sidebar-foreground">σ = {pair.population1.std}</div>
                        </div>
                        <div className="bg-sidebar p-1.5 rounded border border-border">
                          <div className="text-muted-foreground mb-0.5">Population 2</div>
                          <div className="text-sidebar-foreground">μ = {pair.population2.mean}</div>
                          <div className="text-sidebar-foreground">σ = {pair.population2.std}</div>
                        </div>
                      </div>
                      
                      <div className="flex items-center justify-between text-muted-foreground">
                        <span className="text-[10px]">Effect size (d):</span>
                        <span className={`text-[10px] ${
                          Math.abs(effectSize) < 0.2 ? 'text-muted-foreground' :
                          Math.abs(effectSize) < 0.5 ? 'text-sidebar-foreground' :
                          Math.abs(effectSize) < 0.8 ? 'text-sidebar-foreground' :
                          'text-sidebar-foreground'
                        }`}>
                          {effectSize.toFixed(3)}
                        </span>
                      </div>
                    </div>
                  </>
                )}
              </Card>
            );
          })}
        </div>

        {/* Add Pair Form */}
        {showAddPair && (
          <Card className="p-2 bg-sidebar-accent border-border">
            <h4 className="text-xs text-sidebar-foreground mb-2">Add Sample Pair</h4>
            <div className="space-y-3">
              <Input
                label="Pair Name"
                value={newPairName}
                onChange={(e) => handlePairNameChange(e.target.value)}
                placeholder="e.g., Large Effect Test"
                className="text-xs"
                error={getFieldError('newPair.name')}
              />
              
              <div className="grid grid-cols-2 gap-3">
                <div className="space-y-2">
                  <label className="text-xs text-zinc-400">Population 1</label>
                  <Input
                    label="Mean"
                    type="number"
                    value={newPair.population1.mean}
                    onChange={(e) => handlePopulation1MeanChange(e.target.value)}
                    className="text-xs"
                    error={getFieldError('newPair.population1.mean')}
                  />
                  <Input
                    label="Std Dev"
                    type="number"
                    value={newPair.population1.std}
                    onChange={(e) => handlePopulation1StdChange(e.target.value)}
                    className="text-xs"
                    error={getFieldError('newPair.population1.std')}
                  />
                </div>
                
                <div className="space-y-2">
                  <label className="text-xs text-zinc-400">Population 2</label>
                  <Input
                    label="Mean"
                    type="number"
                    value={newPair.population2.mean}
                    onChange={(e) => handlePopulation2MeanChange(e.target.value)}
                    className="text-xs"
                    error={getFieldError('newPair.population2.mean')}
                  />
                  <Input
                    label="Std Dev"
                    type="number"
                    value={newPair.population2.std}
                    onChange={(e) => handlePopulation2StdChange(e.target.value)}
                    className="text-xs"
                    error={getFieldError('newPair.population2.std')}
                  />
                </div>
              </div>
              
              <Input
                label="Sample Size (n)"
                type="number"
                value={newPair.sampleSize}
                onChange={(e) => handleSampleSizeChange(e.target.value)}
                className="text-xs"
                error={getFieldError('newPair.sampleSize')}
              />

              {/* Effect Size Display with Validation */}
              <div className="bg-sidebar p-2 rounded border border-border">
                <div className="flex items-center justify-between">
                  <span className="text-[10px] text-muted-foreground">Effect Size (Cohen's d):</span>
                  <span className={`text-xs ${
                    Math.abs(currentEffectSize) < 0.2 ? 'text-muted-foreground' :
                    Math.abs(currentEffectSize) < 0.5 ? 'text-sidebar-foreground' :
                    Math.abs(currentEffectSize) < 0.8 ? 'text-sidebar-foreground' :
                    'text-sidebar-foreground'
                  }`}>
                    {currentEffectSize.toFixed(3)}
                  </span>
                </div>
                {getFieldWarning('newPair.effectSize') && (
                  <div className="text-[10px] text-destructive mt-0.5">
                    ⚠️ {getFieldWarning('newPair.effectSize')}
                  </div>
                )}
                <div className="text-[10px] text-muted-foreground mt-0.5">
                  {Math.abs(currentEffectSize) < 0.2 ? 'Small effect' :
                   Math.abs(currentEffectSize) < 0.5 ? 'Small-Medium effect' :
                   Math.abs(currentEffectSize) < 0.8 ? 'Medium-Large effect' :
                   'Large effect'}
                </div>
              </div>

              <div className="flex gap-2">
                <Button
                  onClick={handleAddSamplePair}
                  disabled={!newPairName.trim() || validationState.hasErrors}
                  size="sm"
                  className="flex-1 text-xs"
                >
                  Add Pair
                </Button>
                <Button
                  onClick={() => {
                    setShowAddPair(false);
                    clearValidation();
                  }}
                  variant="secondary"
                  size="sm"
                  className="flex-1 text-xs"
                >
                  Cancel
                </Button>
              </div>
            </div>
          </Card>
        )}
      </div>
    );
  };

  const renderSettingsTab = () => {
    if (!currentConfig) {
      return (
        <div className="p-4 text-center text-zinc-400">
          <p className="text-sm">No configuration loaded</p>
        </div>
      );
    }

    const handleSimulationCountChange = (value: string) => {
      const numValue = parseInt(value) || 1000;
      updateConfig({
        simulationCount: numValue,
        updatedAt: new Date()
      });
      validateNumberField(numValue, 'settings.simulationCount', {
        integer: true,
        min: 100,
        max: 10000
      });
    };

    const handleConfidenceLevelChange = (value: string) => {
      const numValue = parseFloat(value) || 0.95;
      updateConfig({
        confidenceLevel: numValue,
        updatedAt: new Date()
      });
      validateNumberField(numValue, 'settings.confidenceLevel', {
        min: 0.01,
        max: 0.999
      });
    };

    const handleAddSignificanceThreshold = () => {
      const newThreshold = 0.05;
      if (!currentConfig.significanceThresholds.includes(newThreshold)) {
        updateConfig({
          significanceThresholds: [...currentConfig.significanceThresholds, newThreshold].sort((a, b) => b - a),
          updatedAt: new Date()
        });
      }
    };

    const handleRemoveSignificanceThreshold = (threshold: number) => {
      if (currentConfig.significanceThresholds.length > 1) {
        updateConfig({
          significanceThresholds: currentConfig.significanceThresholds.filter(t => t !== threshold),
          updatedAt: new Date()
        });
      }
    };

    return (
      <div className="p-4 space-y-4">
        <h3 className="text-sm font-medium text-zinc-100 mb-3">Simulation Parameters</h3>
        
        {/* Simulation Count */}
        <div className="space-y-2">
          <Input
            label="Number of Simulations"
            type="number"
            value={currentConfig.simulationCount}
            onChange={(e) => handleSimulationCountChange(e.target.value)}
            className="text-xs"
            error={getFieldError('settings.simulationCount')}
            helperText="Range: 100 - 10,000 simulations"
          />
        </div>

        {/* Confidence Level */}
        <div className="space-y-2">
          <Input
            label="Confidence Level"
            type="number"
            step="0.01"
            min="0.01"
            max="0.999"
            value={currentConfig.confidenceLevel}
            onChange={(e) => handleConfidenceLevelChange(e.target.value)}
            className="text-xs"
            error={getFieldError('settings.confidenceLevel')}
            helperText={`${(currentConfig.confidenceLevel * 100).toFixed(0)}% confidence intervals`}
          />
        </div>

        {/* Significance Thresholds */}
        <div className="space-y-2">
          <div className="flex items-center justify-between">
            <label className="text-xs font-medium text-zinc-300">Significance Levels (α)</label>
            <Button
              onClick={handleAddSignificanceThreshold}
              size="sm"
              variant="ghost"
              className="text-xs p-1 h-6"
            >
              <Plus className="h-3 w-3" />
            </Button>
          </div>
          
          <div className="space-y-2">
            {currentConfig.significanceThresholds.map((threshold, index) => (
              <div key={index} className="flex items-center gap-2">
                <Input
                  type="number"
                  step="0.001"
                  min="0.001"
                  max="1"
                  value={threshold}
                  onChange={(e) => {
                    const newValue = parseFloat(e.target.value) || threshold;
                    const newThresholds = [...currentConfig.significanceThresholds];
                    newThresholds[index] = newValue;
                    updateConfig({
                      significanceThresholds: newThresholds.sort((a, b) => b - a),
                      updatedAt: new Date()
                    });
                  }}
                  className="text-xs flex-1"
                />
                <button
                  onClick={() => handleRemoveSignificanceThreshold(threshold)}
                  disabled={currentConfig.significanceThresholds.length <= 1}
                  className="p-1 hover:bg-zinc-700 rounded text-zinc-400 hover:text-red-400 disabled:opacity-50 disabled:cursor-not-allowed"
                  title="Remove threshold"
                >
                  <Trash2 className="h-3 w-3" />
                </button>
              </div>
            ))}
          </div>
          <p className="text-xs text-zinc-500">Statistical power will be calculated for each significance level</p>
        </div>

        {/* Statistical Tests Info */}
        <div className="bg-zinc-800 p-3 rounded border border-zinc-600">
          <h4 className="text-xs font-medium text-zinc-300 mb-2">Statistical Tests</h4>
          <div className="space-y-1 text-xs text-zinc-400">
            <div>✓ Welch's t-test (unequal variances)</div>
            <div>✓ Cohen's d effect size</div>
            <div>✓ Confidence intervals</div>
            <div>✓ Statistical power analysis</div>
            <div>✓ Shannon information (S-values)</div>
          </div>
        </div>

        {/* Validation Summary */}
        {validationState.hasErrors && (
          <div className="bg-red-900/20 border border-red-700 rounded-lg p-3">
            <h4 className="text-xs font-medium text-red-400 mb-2">Validation Errors</h4>
            <div className="space-y-1">
              {Object.entries(validationState.errors).map(([field, error]) => (
                <div key={field} className="text-xs text-red-400">
                  • {error}
                </div>
              ))}
            </div>
          </div>
        )}
      </div>
    );
  };
  
  return (
    <div className="h-full flex flex-col bg-sidebar border-l border-border">
      {/* Header */}
      <div className="flex items-center justify-between p-3 border-b border-border">
        <h2 className="text-sidebar-foreground text-sm">Configuration</h2>
        <button 
          onClick={toggleRightSidebar}
          className="h-7 w-7 p-0 flex items-center justify-center rounded hover:bg-sidebar-accent transition-colors text-sidebar-foreground"
        >
          <span className="text-xs">→</span>
        </button>
      </div>
      
      {/* Tab Navigation */}
      <div className="border-b border-border">
        <div className="flex">
          {[
            { id: 'pairs' as const, label: 'Sample Pairs', icon: BarChart3 },
            { id: 'settings' as const, label: 'Parameters', icon: Settings },
            { id: 'visualization' as const, label: 'Charts', icon: BarChart3 }
          ].map((tab) => (
            <button
              key={tab.id}
              onClick={() => setActiveTab(tab.id)}
              className={`flex items-center gap-1 px-2 py-1.5 text-xs transition-colors ${
                activeTab === tab.id
                  ? 'border-b-2 border-sidebar-foreground text-sidebar-foreground bg-sidebar-accent'
                  : 'text-muted-foreground hover:text-sidebar-foreground hover:bg-sidebar-accent/50'
              }`}
            >
              <tab.icon className="h-3 w-3" />
              {tab.label}
            </button>
          ))}
        </div>
      </div>
      
      {/* Tab Content */}
      <div className="flex-1 overflow-auto">
        {activeTab === 'pairs' && renderSamplePairsTab()}
        {activeTab === 'settings' && renderSettingsTab()}
        {activeTab === 'visualization' && (
          <ChartsPanel className="px-3" />
        )}
      </div>
    </div>
  );
}; 
