import { useState, useCallback, useEffect } from 'react';
import { validatePartialSamplePair, validatePopulationConfig } from '../lib/validation';
import type { SamplePair, PopulationConfig } from '../types/index';

interface FieldError {
  field: string;
  message: string;
}

interface ValidationState {
  errors: Record<string, string>;
  warnings: Record<string, string>;
  isValid: boolean;
  hasErrors: boolean;
}

interface UseValidationOptions {
  debounceMs?: number;
  validateOnChange?: boolean;
}

export const useValidation = (options: UseValidationOptions = {}) => {
  const { debounceMs = 300 } = options; // validateOnChange removed as unused
  
  const [validationState, setValidationState] = useState<ValidationState>({
    errors: {},
    warnings: {},
    isValid: true,
    hasErrors: false
  });

  const [debounceTimer, setDebounceTimer] = useState<NodeJS.Timeout | null>(null);

  // Clear validation errors for a specific field
  const clearFieldError = useCallback((fieldPath: string) => {
    setValidationState(prev => {
      const newErrors = { ...prev.errors };
      delete newErrors[fieldPath];
      
      return {
        ...prev,
        errors: newErrors,
        hasErrors: Object.keys(newErrors).length > 0,
        isValid: Object.keys(newErrors).length === 0
      };
    });
  }, []);

  // Set validation error for a specific field
  const setFieldError = useCallback((fieldPath: string, message: string) => {
    setValidationState(prev => ({
      ...prev,
      errors: {
        ...prev.errors,
        [fieldPath]: message
      },
      hasErrors: true,
      isValid: false
    }));
  }, []);

  // Set validation warning for a specific field
  const setFieldWarning = useCallback((fieldPath: string, message: string) => {
    setValidationState(prev => ({
      ...prev,
      warnings: {
        ...prev.warnings,
        [fieldPath]: message
      }
    }));
  }, []);

  // Validate a population configuration
  const validatePopulation = useCallback((
    population: Partial<PopulationConfig>, 
    fieldPrefix: string
  ) => {
    const result = validatePopulationConfig(population);
    
    // Clear previous errors for this population
    setValidationState(prev => {
      const newErrors = { ...prev.errors };
      Object.keys(newErrors).forEach(key => {
        if (key.startsWith(fieldPrefix)) {
          delete newErrors[key];
        }
      });
      return { ...prev, errors: newErrors };
    });

    if (!result.success) {
      result.errors.forEach((error: FieldError) => {
        setFieldError(`${fieldPrefix}.${error.field}`, error.message);
      });
    }

    return result.success;
  }, [setFieldError]);

  // Validate a sample pair
  const validateSamplePairData = useCallback((
    samplePair: Partial<SamplePair>,
    fieldPrefix: string = ''
  ) => {
    const result = validatePartialSamplePair(samplePair);
    
    // Clear previous errors for this sample pair
    setValidationState(prev => {
      const newErrors = { ...prev.errors };
      Object.keys(newErrors).forEach(key => {
        if (fieldPrefix && key.startsWith(fieldPrefix)) {
          delete newErrors[key];
        }
      });
      return { ...prev, errors: newErrors };
    });

    if (!result.success) {
      result.errors.forEach((error: FieldError) => {
        const fullFieldPath = fieldPrefix ? `${fieldPrefix}.${error.field}` : error.field;
        setFieldError(fullFieldPath, error.message);
      });
    }

    return result.success;
  }, [setFieldError]);

  // Debounced validation function
  const validateWithDebounce = useCallback((
    validationFn: () => void
  ) => {
    if (debounceTimer) {
      clearTimeout(debounceTimer);
    }

    const timer = setTimeout(() => {
      validationFn();
    }, debounceMs);

    setDebounceTimer(timer);
  }, [debounceMs, debounceTimer]);

  // Validate number input with range checking
  const validateNumberField = useCallback((
    value: number | string,
    fieldPath: string,
    options: {
      min?: number;
      max?: number;
      integer?: boolean;
      positive?: boolean;
      finite?: boolean;
    } = {}
  ) => {
    const { min, max, integer = false, positive = false, finite = true } = options;
    const numValue = typeof value === 'string' ? parseFloat(value) : value;

    clearFieldError(fieldPath);

    if (isNaN(numValue)) {
      setFieldError(fieldPath, 'Must be a valid number');
      return false;
    }

    if (finite && !isFinite(numValue)) {
      setFieldError(fieldPath, 'Must be a finite number');
      return false;
    }

    if (positive && numValue <= 0) {
      setFieldError(fieldPath, 'Must be positive');
      return false;
    }

    if (integer && !Number.isInteger(numValue)) {
      setFieldError(fieldPath, 'Must be a whole number');
      return false;
    }

    if (min !== undefined && numValue < min) {
      setFieldError(fieldPath, `Must be at least ${min}`);
      return false;
    }

    if (max !== undefined && numValue > max) {
      setFieldError(fieldPath, `Cannot exceed ${max}`);
      return false;
    }

    return true;
  }, [clearFieldError, setFieldError]);

  // Validate string field with length checking
  const validateStringField = useCallback((
    value: string,
    fieldPath: string,
    options: {
      minLength?: number;
      maxLength?: number;
      required?: boolean;
    } = {}
  ) => {
    const { minLength, maxLength, required = false } = options;

    clearFieldError(fieldPath);

    if (required && (!value || value.trim().length === 0)) {
      setFieldError(fieldPath, 'This field is required');
      return false;
    }

    if (minLength !== undefined && value.length < minLength) {
      setFieldError(fieldPath, `Must be at least ${minLength} characters`);
      return false;
    }

    if (maxLength !== undefined && value.length > maxLength) {
      setFieldError(fieldPath, `Cannot exceed ${maxLength} characters`);
      return false;
    }

    return true;
  }, [clearFieldError, setFieldError]);

  // Effect size calculation with warnings
  const calculateEffectSizeWithValidation = useCallback((
    mean1: number,
    mean2: number,
    std1: number,
    std2: number,
    fieldPrefix: string
  ) => {
    const pooledStd = Math.sqrt((std1 ** 2 + std2 ** 2) / 2);
    const effectSize = (mean2 - mean1) / pooledStd;

    // Clear previous warnings
    setValidationState(prev => {
      const newWarnings = { ...prev.warnings };
      delete newWarnings[`${fieldPrefix}.effectSize`];
      return { ...prev, warnings: newWarnings };
    });

    // Add warnings based on effect size
    if (Math.abs(effectSize) < 0.1) {
      setFieldWarning(`${fieldPrefix}.effectSize`, 'Very small effect size (d < 0.1)');
    } else if (Math.abs(effectSize) > 2.0) {
      setFieldWarning(`${fieldPrefix}.effectSize`, 'Very large effect size (d > 2.0) - check parameters');
    }

    return effectSize;
  }, [setFieldWarning]);

  // Clear all validation state
  const clearValidation = useCallback(() => {
    setValidationState({
      errors: {},
      warnings: {},
      isValid: true,
      hasErrors: false
    });
  }, []);

  // Get error for a specific field
  const getFieldError = useCallback((fieldPath: string) => {
    return validationState.errors[fieldPath];
  }, [validationState.errors]);

  // Get warning for a specific field
  const getFieldWarning = useCallback((fieldPath: string) => {
    return validationState.warnings[fieldPath];
  }, [validationState.warnings]);

  // Cleanup debounce timer on unmount
  useEffect(() => {
    return () => {
      if (debounceTimer) {
        clearTimeout(debounceTimer);
      }
    };
  }, [debounceTimer]);

  return {
    validationState,
    validatePopulation,
    validateSamplePairData,
    validateNumberField,
    validateStringField,
    calculateEffectSizeWithValidation,
    validateWithDebounce,
    clearFieldError,
    setFieldError,
    setFieldWarning,
    clearValidation,
    getFieldError,
    getFieldWarning
  };
}; 