import { useState, useCallback, useEffect } from 'react';
import { invoke } from '@tauri-apps/api/core';
import type { 
  FlexibleSimulationConfig, 
  SimulationResults,
  TauriSimulationConfig,
  TauriExportData,
  DesktopFileOperations,
  DesktopNotifications,
  DesktopSystemInfo
} from '../types';

// Check if running in Tauri (desktop) environment
const isDesktop = typeof window !== 'undefined' && '__TAURI__' in window;

// Conversion helpers between frontend and Tauri types
const convertToTauriConfig = (config: FlexibleSimulationConfig): TauriSimulationConfig => ({
  name: config.name,
  template_id: config.id,
  sample_pairs: config.samplePairs.map(pair => ({
    id: pair.id,
    name: pair.name,
    population1_mean: pair.population1.mean,
    population1_std: pair.population1.std,
    population2_mean: pair.population2.mean,
    population2_std: pair.population2.std,
    sample_size: pair.sampleSize,
  })),
  settings: {
    significance_threshold: config.significanceThresholds[0] || 0.05,
    iterations: config.simulationCount,
  },
  created_at: config.createdAt.toISOString(),
  updated_at: config.updatedAt.toISOString(),
});

const convertFromTauriConfig = (tauriConfig: TauriSimulationConfig): FlexibleSimulationConfig => ({
  id: tauriConfig.template_id,
  name: tauriConfig.name,
  samplePairs: tauriConfig.sample_pairs.map(pair => ({
    id: pair.id,
    name: pair.name,
    population1: {
      mean: pair.population1_mean,
      std: pair.population1_std,
      size: 1000, // default
    },
    population2: {
      mean: pair.population2_mean,
      std: pair.population2_std,
      size: 1000, // default
    },
    sampleSize: pair.sample_size,
  })),
  simulationCount: tauriConfig.settings.iterations,
  significanceThresholds: [tauriConfig.settings.significance_threshold],
  confidenceLevel: 0.95, // default
  createdAt: new Date(tauriConfig.created_at),
  updatedAt: new Date(tauriConfig.updated_at),
});

// Native dialog fallback for when Tauri dialog plugin is not available
const showSaveDialogFallback = async (_defaultFilename?: string): Promise<string | null> => {
  // For now, return null - this would need a web-based file save dialog
  console.warn('Native save dialog not available in web environment');
  return null;
};

const showOpenDialogFallback = async (): Promise<string | null> => {
  // For now, return null - this would need a web-based file open dialog
  console.warn('Native open dialog not available in web environment');
  return null;
};

export const useDesktop = () => {
  const [appDataPath, setAppDataPath] = useState<string>('');
  const [isInitialized, setIsInitialized] = useState(false);

  // Initialize desktop environment
  useEffect(() => {
    if (!isDesktop) return;

    const initializeDesktop = async () => {
      try {
        await invoke('create_app_directories');
        const dataPath = await invoke<string>('get_app_data_path');
        setAppDataPath(dataPath);
        setIsInitialized(true);
      } catch (error) {
        console.error('Failed to initialize desktop environment:', error);
      }
    };

    initializeDesktop();
  }, []);

  // File Operations
  const fileOperations: DesktopFileOperations = {
    saveConfig: useCallback(async (config: FlexibleSimulationConfig, filePath?: string) => {
      if (!isDesktop) throw new Error('Desktop operations not available in web environment');
      
      const tauriConfig = convertToTauriConfig(config);
      return await invoke<string>('save_simulation_config', { 
        config: tauriConfig, 
        filePath 
      });
    }, []),

    loadConfig: useCallback(async (filePath: string) => {
      if (!isDesktop) throw new Error('Desktop operations not available in web environment');
      
      const tauriConfig = await invoke<TauriSimulationConfig>('load_simulation_config', { 
        filePath 
      });
      return convertFromTauriConfig(tauriConfig);
    }, []),

    exportResults: useCallback(async (
      config: FlexibleSimulationConfig, 
      results: SimulationResults, 
      format: 'json' | 'csv', 
      filePath?: string
    ) => {
      if (!isDesktop) throw new Error('Desktop operations not available in web environment');
      
      const exportData: TauriExportData = {
        config: convertToTauriConfig(config),
        results: results as unknown as Record<string, unknown>,
        export_format: format,
        timestamp: new Date().toISOString(),
      };
      
      return await invoke<string>('export_simulation_results', { 
        exportData, 
        filePath 
      });
    }, []),

    listSavedConfigs: useCallback(async () => {
      if (!isDesktop) throw new Error('Desktop operations not available in web environment');
      
      return await invoke<string[]>('list_saved_configs');
    }, []),

    showSaveDialog: useCallback(async (defaultFilename?: string) => {
      if (!isDesktop) return showSaveDialogFallback(defaultFilename);
      
      try {
        // Dynamic import for Tauri dialog plugin
        const { save } = await import('@tauri-apps/plugin-dialog');
        const filePath = await save({
          defaultPath: defaultFilename,
          filters: [
            {
              name: 'StatDash Configuration',
              extensions: ['json']
            },
            {
              name: 'All Files',
              extensions: ['*']
            }
          ]
        });
        
        return filePath;
      } catch (error) {
        console.warn('Tauri dialog plugin not available:', error);
        return showSaveDialogFallback(defaultFilename);
      }
    }, []),

    showOpenDialog: useCallback(async () => {
      if (!isDesktop) return showOpenDialogFallback();
      
      try {
        // Dynamic import for Tauri dialog plugin
        const { open } = await import('@tauri-apps/plugin-dialog');
        const filePath = await open({
          multiple: false,
          filters: [
            {
              name: 'StatDash Configuration',
              extensions: ['json']
            },
            {
              name: 'All Files',
              extensions: ['*']
            }
          ]
        });
        
        return Array.isArray(filePath) ? filePath[0] : filePath;
      } catch (error) {
        console.warn('Tauri dialog plugin not available:', error);
        return showOpenDialogFallback();
      }
    }, []),
  };

  // Notifications
  const notifications: DesktopNotifications = {
    send: useCallback(async (title: string, body: string) => {
      if (!isDesktop) {
        // Fallback to web notifications
        if ('Notification' in window && Notification.permission === 'granted') {
          new Notification(title, { body });
        }
        return;
      }
      
      await invoke('send_notification', { title, body });
    }, []),

    sendSimulationComplete: useCallback(async (configName: string, duration: number) => {
      const title = 'Simulation Complete';
      const body = `${configName} finished in ${Math.round(duration / 1000)}s`;
      await notifications.send(title, body);
    }, []),

    sendExportComplete: useCallback(async (filename: string) => {
      const title = 'Export Complete';
      const body = `Results exported to ${filename}`;
      await notifications.send(title, body);
    }, []),
  };

  // System Info
  const systemInfo: DesktopSystemInfo = {
    getAppDataPath: useCallback(async () => {
      if (!isDesktop) return '';
      
      if (appDataPath) return appDataPath;
      
      const dataPath = await invoke<string>('get_app_data_path');
      setAppDataPath(dataPath);
      return dataPath;
    }, [appDataPath]),

    createAppDirectories: useCallback(async () => {
      if (!isDesktop) return;
      
      await invoke('create_app_directories');
    }, []),

    isDesktop,
  };

  return {
    isDesktop,
    isInitialized,
    appDataPath,
    fileOperations,
    notifications,
    systemInfo,
  };
}; 