import { create } from 'zustand';
import { persist } from 'zustand/middleware';

interface UIStore {
  // Sidebar State
  leftSidebarOpen: boolean;
  rightSidebarOpen: boolean;
  
  // Technical Panel State
  technicalPanelExpanded: boolean;
  technicalPanelActiveTab: 'overview' | 'metrics' | 'config';
  
  // Tool State
  activeToolTab: 'datasets' | 'parameters' | 'templates';
  
  // Theme State
  theme: 'light' | 'dark';
  
  // Sidebar Actions
  toggleLeftSidebar: () => void;
  toggleRightSidebar: () => void;
  setLeftSidebarOpen: (open: boolean) => void;
  setRightSidebarOpen: (open: boolean) => void;
  
  // Technical Panel Actions
  setTechnicalPanelExpanded: (expanded: boolean) => void;
  toggleTechnicalPanel: () => void;
  setTechnicalPanelActiveTab: (tab: 'overview' | 'metrics' | 'config') => void;
  
  // Tool Actions
  setActiveToolTab: (tab: 'datasets' | 'parameters' | 'templates') => void;
  
  // Theme Actions
  setTheme: (theme: 'light' | 'dark') => void;
  initializeTheme: () => void;
  
  // Responsive Actions
  closeSidebarsOnMobile: () => void;
  resetToDefaults: () => void;
}

export const useUIStore = create<UIStore>()(
  persist(
    (set, get) => ({
      // Initial State
      leftSidebarOpen: true,
      rightSidebarOpen: true,
      technicalPanelExpanded: false,
      technicalPanelActiveTab: 'overview',
      activeToolTab: 'datasets',
      theme: 'dark',
      
      // Sidebar Actions
      toggleLeftSidebar: () => set(state => ({ 
        leftSidebarOpen: !state.leftSidebarOpen 
      })),
      
      toggleRightSidebar: () => set(state => ({ 
        rightSidebarOpen: !state.rightSidebarOpen 
      })),
      
      setLeftSidebarOpen: (open: boolean) => set({ leftSidebarOpen: open }),
      
      setRightSidebarOpen: (open: boolean) => set({ rightSidebarOpen: open }),
      
      // Technical Panel Actions
      setTechnicalPanelExpanded: (expanded: boolean) => set({ technicalPanelExpanded: expanded }),
      
      toggleTechnicalPanel: () => set(state => ({ 
        technicalPanelExpanded: !state.technicalPanelExpanded 
      })),
      
      setTechnicalPanelActiveTab: (tab: 'overview' | 'metrics' | 'config') => 
        set({ technicalPanelActiveTab: tab }),
      
      // Tool Actions
      setActiveToolTab: (tab: 'datasets' | 'parameters' | 'templates') => 
        set({ activeToolTab: tab }),
      
      // Theme Actions
      setTheme: (theme: 'light' | 'dark') => {
        set({ theme });
        // Apply theme to document
        if (theme === 'dark') {
          document.documentElement.classList.add('dark');
        } else {
          document.documentElement.classList.remove('dark');
        }
      },
      
      initializeTheme: () => {
        const { theme } = get();
        // Force dark theme by default
        document.documentElement.classList.add('dark');
        if (theme === 'light') {
          document.documentElement.classList.remove('dark');
        }
      },
      
      // Responsive Actions
      closeSidebarsOnMobile: () => {
        const isMobile = window.innerWidth < 768;
        if (isMobile) {
          set({ leftSidebarOpen: false, rightSidebarOpen: false });
        }
      },
      
      resetToDefaults: () => set({
        leftSidebarOpen: true,
        rightSidebarOpen: true,
        technicalPanelExpanded: false,
        technicalPanelActiveTab: 'overview',
        activeToolTab: 'datasets',
        theme: 'dark'
      })
    }),
    {
      name: 'statdash-ui-storage',
      partialize: (state) => ({
        leftSidebarOpen: state.leftSidebarOpen,
        rightSidebarOpen: state.rightSidebarOpen,
        technicalPanelExpanded: state.technicalPanelExpanded,
        technicalPanelActiveTab: state.technicalPanelActiveTab,
        activeToolTab: state.activeToolTab,
        theme: state.theme
      }),
      version: 1,
    }
  )
);

// Selectors for better performance
export const useLeftSidebarOpen = () => useUIStore(state => state.leftSidebarOpen);
export const useRightSidebarOpen = () => useUIStore(state => state.rightSidebarOpen);
export const useTechnicalPanelExpanded = () => useUIStore(state => state.technicalPanelExpanded);
export const useTechnicalPanelActiveTab = () => useUIStore(state => state.technicalPanelActiveTab);
export const useActiveToolTab = () => useUIStore(state => state.activeToolTab);
export const useTheme = () => useUIStore(state => state.theme);

// Action selectors
export const useUIActions = () => useUIStore(state => ({
  toggleLeftSidebar: state.toggleLeftSidebar,
  toggleRightSidebar: state.toggleRightSidebar,
  setLeftSidebarOpen: state.setLeftSidebarOpen,
  setRightSidebarOpen: state.setRightSidebarOpen,
  setTechnicalPanelExpanded: state.setTechnicalPanelExpanded,
  toggleTechnicalPanel: state.toggleTechnicalPanel,
  setTechnicalPanelActiveTab: state.setTechnicalPanelActiveTab,
  setActiveToolTab: state.setActiveToolTab,
  setTheme: state.setTheme,
  closeSidebarsOnMobile: state.closeSidebarsOnMobile,
  resetToDefaults: state.resetToDefaults
})); 