import { z } from 'zod';

// Population configuration validation
export const PopulationConfigSchema = z.object({
  mean: z.number().finite('Mean must be a finite number'),
  std: z.number().positive('Standard deviation must be positive'),
  size: z.number().int().min(10, 'Population size must be at least 10').max(10000, 'Population size cannot exceed 10,000')
});

// Sample pair validation
export const SamplePairSchema = z.object({
  id: z.string().min(1, 'ID is required'),
  name: z.string().min(1, 'Name is required').max(50, 'Name cannot exceed 50 characters'),
  population1: PopulationConfigSchema,
  population2: PopulationConfigSchema,
  sampleSize: z.number().int().min(5, 'Sample size must be at least 5').max(1000, 'Sample size cannot exceed 1,000')
});

// Simulation configuration validation
export const FlexibleSimulationConfigSchema = z.object({
  id: z.string().min(1, 'ID is required'),
  name: z.string().min(1, 'Configuration name is required').max(100, 'Name cannot exceed 100 characters'),
  samplePairs: z.array(SamplePairSchema).min(1, 'At least one sample pair is required').max(20, 'Cannot exceed 20 sample pairs'),
  simulationCount: z.number().int().min(100, 'Minimum 100 simulations required').max(10000, 'Maximum 10,000 simulations allowed'),
  significanceThresholds: z.array(
    z.number().min(0.001, 'Threshold must be at least 0.001').max(1, 'Threshold cannot exceed 1')
  ).min(1, 'At least one significance threshold is required'),
  confidenceLevel: z.number().min(0.01, 'Confidence level must be at least 0.01').max(0.999, 'Confidence level cannot exceed 0.999'),
  createdAt: z.date(),
  updatedAt: z.date()
});

// Template validation
export const ConfigurationTemplateSchema = z.object({
  id: z.string().min(1, 'Template ID is required'),
  name: z.string().min(1, 'Template name is required').max(100, 'Name cannot exceed 100 characters'),
  description: z.string().max(500, 'Description cannot exceed 500 characters'),
  config: FlexibleSimulationConfigSchema,
  isBuiltIn: z.boolean()
});

// Partial schemas for form validation (allowing incomplete data during editing)
export const PartialPopulationConfigSchema = z.object({
  mean: z.number().finite().optional(),
  std: z.number().positive().optional(),
  size: z.number().int().min(10).max(10000).optional()
});

export const PartialSamplePairSchema = z.object({
  id: z.string().optional(),
  name: z.string().max(50).optional(),
  population1: PartialPopulationConfigSchema.optional(),
  population2: PartialPopulationConfigSchema.optional(),
  sampleSize: z.number().int().min(5).max(1000).optional()
});

// Form validation helpers
export function validatePopulationConfig(data: unknown) {
  try {
    const result = PopulationConfigSchema.parse(data);
    return { success: true, data: result, errors: [] };
  } catch (error) {
    if (error instanceof z.ZodError) {
      return { 
        success: false, 
        data: null, 
        errors: error.errors.map(err => ({
          field: err.path.join('.'),
          message: err.message,
          value: undefined
        }))
      };
    }
    return { success: false, data: null, errors: [{ field: 'unknown', message: 'Validation failed', value: data }] };
  }
}

