import React, { useEffect } from 'react';
import { useCurrentConfig, useProgress, useResults } from '../../store/index';
import { useSimulationWorker } from '../../hooks/useSimulationWorker';
import { validateSimulationConfig } from '../../lib/validation';
import { SimulationResults } from '../SimulationResults';
import { useChartsStore } from '../../stores/chartsStore';
import { useUIStore, useTechnicalPanelExpanded } from '../../stores/uiStore';
import { 
  BarChart3, 
  Play, 
  Square, 
  AlertCircle, 
  CheckCircle, 
  ChevronDown, 
  ChevronRight
} from 'lucide-react';
import { Button } from '../ui/Button';

export const CentralWorkspace: React.FC = () => {
  const currentConfig = useCurrentConfig();
  const progress = useProgress();
  const results = useResults();
  const { runSimulation, cancelSimulation, isRunning, simulationState } = useSimulationWorker();
  const updateCurrentResults = useChartsStore(state => state.updateCurrentResults);
  
  // Technical panel state
  const technicalPanelExpanded = useTechnicalPanelExpanded();
  const setTechnicalPanelExpanded = useUIStore(state => state.setTechnicalPanelExpanded);
  
  // Update charts store when final results are available (simulation finished)
  useEffect(() => {
    if (results && !isRunning) {
      updateCurrentResults(results);
    }
  }, [results, isRunning, updateCurrentResults]);

  // Check if configuration is valid for simulation
  const isConfigValid = currentConfig && 
    currentConfig.samplePairs.length > 0 && 
    currentConfig.samplePairs.every(pair => 
      pair.name.trim().length > 0 &&
      pair.population1.std > 0 &&
      pair.population2.std > 0 &&
      pair.sampleSize >= 5
    );

  // Smart auto-expand/collapse behavior
  useEffect(() => {
    if (!currentConfig) return;
    
    const hasValidationErrors = !isConfigValid;
    
    // Auto-expand when there are validation errors
    if (hasValidationErrors && !technicalPanelExpanded) {
      setTechnicalPanelExpanded(true);
    }
    
    // Auto-collapse when simulation starts running (focus on results)
    if (isRunning && technicalPanelExpanded) {
      setTechnicalPanelExpanded(false);
    }
  }, [isRunning, currentConfig, technicalPanelExpanded, setTechnicalPanelExpanded, isConfigValid]);

  const handleRunSimulation = async () => {
    console.log('handleRunSimulation called');
    if (!currentConfig) {
      console.log('No current config, returning');
      return;
    }
    
    // Validate configuration before running
    const configValidation = validateSimulationConfig(currentConfig);
    if (!configValidation.success) {
      console.error('Configuration validation failed:', configValidation.errors);
      return;
    }
    
    console.log('Starting simulation with valid config');
    try {
      await runSimulation(currentConfig);
    } catch (error) {
      console.error('Simulation failed:', error);
    }
  };

  const handleCancelSimulation = () => {
    console.log('handleCancelSimulation called');
    cancelSimulation();
  };

  // Context-aware button logic
  const getContextButton = () => {
    if (isRunning) {
      return (
        <Button
          variant="secondary"
          size="sm"
          onClick={handleCancelSimulation}
          className="flex items-center gap-1.5 text-xs px-3 py-1.5"
        >
          <Square className="h-3 w-3" />
          Cancel ({(progress.progress * 100).toFixed(0)}%)
        </Button>
      );
    }
    
    // Always show "Run Simulation" when not running (allows re-running with different parameters)
    return (
      <Button
        variant="primary"
        size="sm"
        onClick={handleRunSimulation}
        disabled={!isConfigValid}
        className="flex items-center gap-1.5 text-xs px-3 py-1.5"
      >
        <Play className="h-3 w-3" />
        Run Simulation
      </Button>
    );
  };

  const renderTechnicalPanel = () => {
    if (!currentConfig) return null;

    const formatNumber = (num: number, decimals: number = 3) => num.toFixed(decimals);

    return (
      <div className="border-b border-border bg-card">
        {/* Collapsible Header */}
        <div 
          className="flex items-center justify-between p-3 cursor-pointer hover:bg-muted/50 transition-colors"
          onClick={() => setTechnicalPanelExpanded(!technicalPanelExpanded)}
        >
          <div className="flex items-center gap-2">
            {technicalPanelExpanded ? (
              <ChevronDown className="h-3 w-3 text-muted-foreground" />
            ) : (
              <ChevronRight className="h-3 w-3 text-muted-foreground" />
            )}
            <h3 className="text-foreground text-xs">Technical Information</h3>
            {!isConfigValid && (
              <div className="h-1.5 w-1.5 bg-destructive rounded-full" />
            )}
          </div>
          
          {/* Summary when collapsed */}
          {!technicalPanelExpanded && (
            <div className="flex items-center gap-2 text-[10px] text-muted-foreground">
              <span>{currentConfig.samplePairs.length} pairs</span>
              {results && (
                <>
                  <span>•</span>
                  <span>{((results.pValues.filter(p => p < 0.05).length / results.pValues.length) * 100).toFixed(0)}% power</span>
                  <span>•</span>
                  <span>d={formatNumber(results.meanEffectSize, 2)}</span>
                </>
              )}
              <span>•</span>
              <span>{currentConfig.simulationCount.toLocaleString()} sims</span>
            </div>
          )}
        </div>

        {/* Collapsible Content - Single Organized Container */}
        {technicalPanelExpanded && (
          <div className="border-t border-border p-3">
            <div className="grid grid-cols-12 gap-3">
              
              {/* Sample Pairs - Left Section */}
              <div className="col-span-5">
                <h4 className="text-xs text-foreground mb-2">Sample Pairs ({currentConfig.samplePairs.length})</h4>
                {currentConfig.samplePairs.length === 0 ? (
                  <div className="text-center py-2 text-muted-foreground">
                    <p className="text-[10px]">No sample pairs configured</p>
                  </div>
                ) : (
                  <div className="space-y-1 max-h-20 overflow-y-auto">
                    {currentConfig.samplePairs.map((pair) => {
                      const effectSize = (pair.population2.mean - pair.population1.mean) / 
                        Math.sqrt((pair.population1.std ** 2 + pair.population2.std ** 2) / 2);
                      
                      const hasValidationIssues = 
                        pair.name.trim().length === 0 ||
                        pair.population1.std <= 0 ||
                        pair.population2.std <= 0 ||
                        pair.sampleSize < 5;

                      return (
                        <div key={pair.id} className={`bg-muted p-1 rounded text-[10px] ${
                          hasValidationIssues ? 'border border-destructive' : ''
                        }`}>
                          <div className="flex items-center justify-between">
                            <span className="text-foreground truncate flex-1">{pair.name || 'Unnamed'}</span>
                            <span className="text-muted-foreground ml-2">n={pair.sampleSize}, d={effectSize.toFixed(2)}</span>
                            {hasValidationIssues && <AlertCircle className="h-2 w-2 text-destructive ml-1" />}
                          </div>
                        </div>
                      );
                    })}
                  </div>
                )}
              </div>

              {/* Statistical Metrics - Center Section */}
              {results && (
                <div className="col-span-4">
                  <h4 className="text-xs text-foreground mb-2">Results Summary</h4>
                  <div className="grid grid-cols-1 gap-1">
                    <div className="bg-muted rounded p-1.5 text-[10px]">
                      <div className="flex items-center justify-between">
                        <span className="text-muted-foreground">Statistical Power:</span>
                        <span className="text-foreground">{((results.pValues.filter(p => p < 0.05).length / results.pValues.length) * 100).toFixed(1)}%</span>
                      </div>
                    </div>
                    <div className="bg-muted rounded p-1.5 text-[10px]">
                      <div className="flex items-center justify-between">
                        <span className="text-muted-foreground">Effect Size:</span>
                        <span className="text-foreground">{formatNumber(results.meanEffectSize, 3)}</span>
                      </div>
                    </div>
                    <div className="bg-muted rounded p-1.5 text-[10px]">
                      <div className="flex items-center justify-between">
                        <span className="text-muted-foreground">CI Precision:</span>
                        <span className="text-foreground">
                          {results.ciGaps ? formatNumber(results.ciGaps.reduce((sum, gap) => sum + gap, 0) / results.ciGaps.length, 3) : 'N/A'}
                        </span>
                      </div>
                    </div>
                  </div>
                </div>
              )}

              {/* Configuration - Right Section */}
              <div className="col-span-3">
                <h4 className="text-xs text-foreground mb-2">Configuration</h4>
                <div className="space-y-1 text-[10px]">
                  <div className="flex justify-between">
                    <span className="text-muted-foreground">Simulations:</span>
                    <span className="text-foreground">{currentConfig.simulationCount.toLocaleString()}</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-muted-foreground">Confidence:</span>
                    <span className="text-foreground">{(currentConfig.confidenceLevel * 100).toFixed(0)}%</span>
                  </div>
                  <div className="bg-muted rounded p-1">
                    <div className="text-muted-foreground mb-0.5">Significance α:</div>
                    <div className="text-foreground">{currentConfig.significanceThresholds.join(', ')}</div>
                  </div>
                </div>
              </div>
            </div>

            {/* Validation Issues - Full Width at Bottom */}
            {!isConfigValid && (
              <div className="mt-3 bg-destructive/10 border border-destructive/20 rounded p-2">
                <h4 className="text-[10px] text-destructive mb-1">⚠️ Configuration Issues</h4>
                <div className="text-[10px] text-destructive space-y-0.5">
                  {currentConfig.samplePairs.length === 0 && (
                    <div>• Add at least one sample pair to run simulations</div>
                  )}
                  {currentConfig.samplePairs.some(pair => pair.name.trim().length === 0) && (
                    <div>• All sample pairs must have names</div>
                  )}
                  {currentConfig.samplePairs.some(pair => pair.population1.std <= 0 || pair.population2.std <= 0) && (
                    <div>• Standard deviations must be positive</div>
                  )}
                  {currentConfig.samplePairs.some(pair => pair.sampleSize < 5) && (
                    <div>• Sample sizes must be at least 5</div>
                  )}
                </div>
              </div>
            )}
          </div>
        )}
      </div>
    );
  };

  const renderContent = () => {
    if (!currentConfig) {
      return (
        <div className="flex-1 flex flex-col items-center justify-center p-4">
          <div className="text-center max-w-md">
            <BarChart3 className="h-12 w-12 mx-auto mb-3 text-muted-foreground" />
            <h2 className="text-lg text-foreground mb-2">Welcome to StatDash</h2>
            <p className="text-muted-foreground mb-4 text-sm">
              A statistical simulation platform for exploring hypothesis testing, effect sizes, and statistical power.
            </p>
            
            <div className="space-y-3">
              <div className="p-3 bg-card rounded border border-border">
                <h3 className="text-foreground mb-2 text-sm">🚀 Quick Start</h3>
                <p className="text-xs text-muted-foreground mb-2">
                  Get started by creating a new simulation or loading a template from the left sidebar.
                </p>
                <div className="text-[10px] text-muted-foreground space-y-0.5">
                  <div>• Create simulation configurations</div>
                  <div>• Add multiple sample pairs</div>
                  <div>• Run statistical simulations</div>
                  <div>• Analyze power and effect sizes</div>
                </div>
              </div>
              
              <div className="p-3 bg-card rounded border border-border">
                <h3 className="text-foreground mb-2 text-sm">📋 Built-in Templates</h3>
                <p className="text-xs text-muted-foreground mb-2">
                  Use pre-configured templates for common simulation scenarios:
                </p>
                <div className="text-[10px] text-muted-foreground space-y-0.5">
                  <div>• Legacy Notebook Reproduction</div>
                  <div>• Null Hypothesis Testing</div>
                  <div>• Effect Size Comparison</div>
                </div>
              </div>
            </div>
          </div>
        </div>
      );
    }

    // Show simulation interface when config is loaded
    return (
      <div className="flex-1 overflow-auto">
        {/* Technical Panel */}
        {renderTechnicalPanel()}

          {/* Results Display */}
        <div className="p-3">
          {results && (
            <SimulationResults results={results} />
          )}
        </div>
      </div>
    );
  };

  return (
    <div className="flex flex-col h-full bg-background">
      {/* Enhanced Header Bar with Simulation Controls */}
      {currentConfig && (
        <div className="flex items-center justify-between p-3 border-b border-border bg-card">
          <div className="flex items-center gap-2">
            <BarChart3 className="h-4 w-4 text-muted-foreground" />
            <div>
              <h1 className="text-foreground text-sm">{currentConfig.name}</h1>
              <p className="text-[10px] text-muted-foreground">
                {currentConfig.samplePairs.length} sample pair{currentConfig.samplePairs.length !== 1 ? 's' : ''} • {currentConfig.simulationCount.toLocaleString()} simulations
              </p>
            </div>
          </div>
          
          <div className="flex items-center gap-2">
            {/* Status Indicators */}
            <div className="flex items-center gap-1.5">
            {isRunning && (
                <div className="flex items-center gap-1.5 text-foreground">
                  <div className="h-1.5 w-1.5 bg-foreground rounded-full animate-pulse" />
                  <span className="text-[10px]">Running</span>
              </div>
            )}
            {isConfigValid && !isRunning && (
                <div className="flex items-center gap-1.5 text-foreground">
                  <CheckCircle className="h-2.5 w-2.5" />
                  <span className="text-[10px]">Valid</span>
              </div>
            )}
            {!isConfigValid && (
                <div className="flex items-center gap-1.5 text-destructive">
                  <AlertCircle className="h-2.5 w-2.5" />
                  <span className="text-[10px]">Issues</span>
              </div>
            )}
              <span className="text-[10px] text-muted-foreground">
              Confidence: {(currentConfig.confidenceLevel * 100).toFixed(0)}%
            </span>
            </div>

            {/* Context-Aware Action Button */}
            {getContextButton()}
          </div>
        </div>
      )}

      {/* Integrated Progress Bar */}
      {isRunning && (
        <div className="h-0.5 bg-muted relative overflow-hidden">
          <div 
            className="h-full bg-primary transition-all duration-300"
            style={{ width: `${(progress.progress * 100).toFixed(1)}%` }}
          />
        </div>
      )}

      {/* Error Display */}
      {simulationState.error && (
        <div className="mx-3 mt-2 p-2 bg-destructive/10 border border-destructive/20 rounded flex items-center gap-2">
          <AlertCircle className="h-3 w-3 text-destructive" />
          <span className="text-destructive text-xs">{simulationState.error}</span>
        </div>
      )}

      {/* Content Area */}
      {renderContent()}
    </div>
  );
}; 