import React, { useState, useRef, useEffect } from 'react';
import { motion, AnimatePresence } from 'motion/react';
import { EffectSizeDistribution } from './EffectSizeDistribution';
import { ChevronLeft, ChevronRight, Play, Pause } from 'lucide-react';

interface EffectSizeDataset {
  effectSizes: number[];
  sampleSize: number;
  label: string;
}

interface HorizontalEffectSizeCarouselProps {
  datasets: EffectSizeDataset[];
  trueEffectSize: number;
}

export const HorizontalEffectSizeCarousel: React.FC<HorizontalEffectSizeCarouselProps> = ({
  datasets,
  trueEffectSize
}) => {
  const [currentIndex, setCurrentIndex] = useState(0);
  const [isPlaying, setIsPlaying] = useState(false);
  const intervalRef = useRef<NodeJS.Timeout | null>(null);

  // Auto-play functionality
  useEffect(() => {
    if (isPlaying) {
      intervalRef.current = setInterval(() => {
        setCurrentIndex((prev) => (prev + 1) % datasets.length);
      }, 2500); // Slightly slower for effect sizes (more time to observe precision)
    } else {
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
        intervalRef.current = null;
      }
    }

    return () => {
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
      }
    };
  }, [isPlaying, datasets.length]);

  const goToPrevious = () => {
    setCurrentIndex((prev) => (prev - 1 + datasets.length) % datasets.length);
  };

  const goToNext = () => {
    setCurrentIndex((prev) => (prev + 1) % datasets.length);
  };

  const togglePlayPause = () => {
    setIsPlaying(!isPlaying);
  };

  if (!datasets || datasets.length === 0) {
    return null;
  }

  return (
    <div className="space-y-4">
      {/* Main Carousel Container */}
      <div className="relative overflow-hidden">
        <AnimatePresence mode="wait">
          <motion.div
            key={currentIndex}
            initial={{ x: 300, opacity: 0, scale: 0.95 }}
            animate={{ x: 0, opacity: 1, scale: 1 }}
            exit={{ x: -300, opacity: 0, scale: 0.95 }}
            transition={{ 
              type: "spring",
              stiffness: 250,
              damping: 25
            }}
            className="bg-zinc-800 rounded-lg p-6"
          >
            <div className="text-center mb-4">
              <h4 className="text-lg font-semibold text-zinc-100">
                {datasets[currentIndex].label}
              </h4>
              <p className="text-sm text-zinc-400">
                {datasets[currentIndex].effectSizes.length} effect size estimates
              </p>
              <div className="text-xs text-purple-400 mt-1">
                Precision increases with sample size →
              </div>
            </div>
            
            <EffectSizeDistribution
              datasets={[datasets[currentIndex]]}
              title=""
              trueEffectSize={trueEffectSize}
            />
            
            {/* Individual Statistics */}
            <motion.div 
              className="mt-4 grid grid-cols-2 gap-4 text-sm"
              initial={{ opacity: 0, y: 10 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ delay: 0.2 }}
            >
              <div className="bg-zinc-700 rounded p-3">
                <div className="text-zinc-400">Mean Effect Size</div>
                <div className="text-zinc-100 font-semibold">
                  {(datasets[currentIndex].effectSizes.reduce((sum, es) => sum + es, 0) / datasets[currentIndex].effectSizes.length).toFixed(3)}
                </div>
              </div>
              <div className="bg-zinc-700 rounded p-3">
                <div className="text-zinc-400">Standard Error</div>
                <div className="text-zinc-100 font-semibold">
                  {calculateStandardError(datasets[currentIndex].effectSizes).toFixed(3)}
                </div>
              </div>
            </motion.div>
          </motion.div>
        </AnimatePresence>
      </div>

      {/* Navigation Controls */}
      <div className="flex items-center justify-center space-x-4">
        <motion.button
          onClick={goToPrevious}
          className="p-2 bg-zinc-700 hover:bg-zinc-600 rounded-full text-zinc-300 hover:text-white transition-colors"
          whileHover={{ scale: 1.1 }}
          whileTap={{ scale: 0.9 }}
        >
          <ChevronLeft className="h-5 w-5" />
        </motion.button>

        <motion.button
          onClick={togglePlayPause}
          className={`p-2 rounded-full transition-colors ${
            isPlaying 
              ? 'bg-red-600 hover:bg-red-500 text-white' 
              : 'bg-purple-600 hover:bg-purple-500 text-white'
          }`}
          whileHover={{ scale: 1.1 }}
          whileTap={{ scale: 0.9 }}
        >
          {isPlaying ? <Pause className="h-5 w-5" /> : <Play className="h-5 w-5" />}
        </motion.button>

        <motion.button
          onClick={goToNext}
          className="p-2 bg-zinc-700 hover:bg-zinc-600 rounded-full text-zinc-300 hover:text-white transition-colors"
          whileHover={{ scale: 1.1 }}
          whileTap={{ scale: 0.9 }}
        >
          <ChevronRight className="h-5 w-5" />
        </motion.button>
      </div>

      {/* Enhanced Progress Indicator */}
      <div className="space-y-2">
        <div className="flex justify-center space-x-2">
          {datasets.map((dataset, index) => (
            <motion.button
              key={index}
              onClick={() => setCurrentIndex(index)}
              className={`px-2 py-1 rounded text-xs font-medium transition-colors ${
                index === currentIndex 
                  ? 'bg-purple-500 text-white' 
                  : 'bg-zinc-600 hover:bg-zinc-500 text-zinc-300'
              }`}
              whileHover={{ scale: 1.1 }}
              whileTap={{ scale: 0.9 }}
            >
              {dataset.label}
            </motion.button>
          ))}
        </div>
        
        {/* Precision Timeline */}
        <div className="relative">
          <div className="h-2 bg-zinc-700 rounded-full overflow-hidden">
            <motion.div
              className="h-full bg-gradient-to-r from-red-500 via-yellow-500 to-green-500 rounded-full"
              animate={{
                width: `${((currentIndex + 1) / datasets.length) * 100}%`
              }}
              transition={{ type: "spring", stiffness: 400, damping: 30 }}
            />
          </div>
          
          {/* Precision Labels */}
          <div className="flex justify-between text-xs text-zinc-500 mt-1">
            <span>Lower Precision</span>
            <span>Higher Precision</span>
          </div>
        </div>
      </div>

      {/* Educational Context */}
      <div className="bg-purple-900/20 border border-purple-800/30 rounded-lg p-3 text-sm">
        <p className="text-purple-200">
          <strong>Effect Size Progression:</strong> Watch how the distribution becomes tighter around the true effect size as sample size increases. 
          The gradient bar shows the progression from lower to higher precision.
        </p>
      </div>
    </div>
  );
};

// Helper function to calculate standard error
const calculateStandardError = (values: number[]): number => {
  if (values.length === 0) return 0;
  
  const mean = values.reduce((sum, val) => sum + val, 0) / values.length;
  const variance = values.reduce((sum, val) => sum + Math.pow(val - mean, 2), 0) / values.length;
  const standardDeviation = Math.sqrt(variance);
  
  return standardDeviation / Math.sqrt(values.length);
}; 