import React, { useState, useMemo } from 'react';
import { motion, AnimatePresence } from 'motion/react';
import { Bar } from 'react-chartjs-2';
import { EffectSizeDistribution } from './EffectSizeDistribution';
import { HorizontalEffectSizeCarousel } from './HorizontalEffectSizeCarousel';
import { LayoutGrid, Layers } from 'lucide-react';

interface EffectSizeDataset {
  effectSizes: number[];
  sampleSize: number;
  label: string;
}

interface AnimatedEffectSizePanelProps {
  datasets: EffectSizeDataset[];
  title: string;
  trueEffectSize: number;
  initialMode?: 'separate' | 'overlay';
}

export const AnimatedEffectSizePanel: React.FC<AnimatedEffectSizePanelProps> = ({
  datasets,
  title,
  trueEffectSize,
  initialMode = 'separate'
}) => {
  const [mode, setMode] = useState<'separate' | 'overlay'>(initialMode);
  
  // Don't render if no datasets or only one dataset
  if (!datasets || datasets.length <= 1) {
    return (
      <div className="bg-zinc-800 rounded-lg p-1">
        <EffectSizeDistribution
          datasets={datasets || []}
          title={title}
          trueEffectSize={trueEffectSize}
        />
      </div>
    );
  }

  return (
    <div className="space-y-4">
      {/* Mode Toggle */}
      <div className="flex items-center justify-between">
        <h3 className="text-lg font-semibold text-zinc-100">{title}</h3>
        <div className="flex items-center bg-zinc-800 rounded-lg relative overflow-hidden">
          {/* Animated background */}
          <motion.div
            className="absolute top-0 left-0 w-1/2 h-full bg-purple-600"
            animate={{ x: mode === 'separate' ? '0%' : '100%' }}
            transition={{ type: 'spring', stiffness: 300, damping: 30 }}
            layoutId="effectSizeToggle"
          />
          <motion.button
            onClick={() => setMode('separate')}
            className={`relative z-10 flex items-center gap-2 px-3 py-1.5 rounded text-sm font-medium transition-colors ${
              mode === 'separate'
                ? 'text-white'
                : 'text-zinc-400 hover:text-zinc-200'
            }`}
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
          >
            <LayoutGrid className="h-4 w-4" />
            Slideshow
          </motion.button>
          
          <motion.button
            onClick={() => setMode('overlay')}
            className={`relative z-10 flex items-center gap-2 px-3 py-1.5 rounded text-sm font-medium transition-colors ${
              mode === 'overlay'
                ? 'text-white'
                : 'text-zinc-400 hover:text-zinc-200'
            }`}
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
          >
            <Layers className="h-4 w-4" />
            Overlay
          </motion.button>
        </div>
      </div>

      {/* Animated Container with Motion */}
      <div className="relative overflow-hidden rounded-lg">
        <AnimatePresence mode="wait">
          {mode === 'separate' ? (
            <motion.div
              key="separate"
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: -20 }}
              transition={{ duration: 0.4, ease: "easeInOut" }}
              layout
            >
              {/* Separate Mode: Horizontal Slideshow */}
              <HorizontalEffectSizeCarousel
                datasets={datasets}
                trueEffectSize={trueEffectSize}
              />
            </motion.div>
          ) : (
            <motion.div
              key="overlay"
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: -20 }}
              transition={{ duration: 0.4, ease: "easeInOut" }}
              layout
            >
              {/* Overlay Mode: Compact Overlapping Chart */}
              <motion.div 
                className="bg-zinc-800 rounded-lg p-4 overflow-hidden"
                initial={{ scale: 0.95, opacity: 0 }}
                animate={{ scale: 1, opacity: 1 }}
                transition={{ delay: 0.1, duration: 0.3 }}
                whileHover={{ scale: 1.01 }}
              >
                <OverlappingEffectSizeDistribution
                  datasets={datasets}
                  trueEffectSize={trueEffectSize}
                />
                <motion.div 
                  className="flex justify-center mt-4 space-x-4"
                  initial={{ opacity: 0, y: 10 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ delay: 0.3, duration: 0.3 }}
                >
                  {datasets.map((dataset, index) => (
                    <motion.div 
                      key={`legend-${dataset.sampleSize}`} 
                      className="flex items-center gap-2"
                      initial={{ opacity: 0, x: -10 }}
                      animate={{ opacity: 1, x: 0 }}
                      transition={{ delay: 0.4 + index * 0.05 }}
                      whileHover={{ scale: 1.05 }}
                    >
                      <motion.div 
                        className="w-3 h-3 rounded"
                        style={{ backgroundColor: `rgba(255, 255, 255, ${0.9 - index * 0.2})` }}
                        whileHover={{ scale: 1.2 }}
                      />
                      <span className="text-sm text-zinc-300">{dataset.label}</span>
                    </motion.div>
                  ))}
                </motion.div>
              </motion.div>
            </motion.div>
          )}
        </AnimatePresence>
      </div>

      {/* Educational Context */}
      <div className="bg-purple-900/20 border border-purple-800/30 rounded-lg p-3 text-sm">
        <p className="text-purple-200">
          <strong>{mode === 'separate' ? 'Separate View:' : 'Overlay View:'}</strong>{' '}
          {mode === 'separate'
            ? 'Analyze how sample size affects the precision of effect size estimates for each group individually.'
            : 'Compare effect size precision across all sample sizes simultaneously to identify optimal power.'}
        </p>
      </div>
    </div>
  );
};

// Overlapping effect size distribution using Chart.js - Shannon/CI Gap style
const OverlappingEffectSizeDistribution: React.FC<{
  datasets: EffectSizeDataset[];
  trueEffectSize: number;
}> = ({ datasets, trueEffectSize }) => {
  const { chartData } = useMemo(() => {
    const binCount = 30; // Fewer bins for effect size
    const minEffect = Math.min(...datasets.flatMap(d => d.effectSizes));
    const maxEffect = Math.max(...datasets.flatMap(d => d.effectSizes));
    const range = maxEffect - minEffect;
    const binWidth = range / binCount;
    
    // Create bin labels
    const labels = Array(binCount).fill(0).map((_, i) => {
      const binStart = minEffect + i * binWidth;
      const binEnd = minEffect + (i + 1) * binWidth;
      return `${binStart.toFixed(2)}-${binEnd.toFixed(2)}`;
    });

    // Create datasets for each sample size with white fills at different opacities
    const chartDatasets = datasets.map((dataset, index) => {
      const bins = Array(binCount).fill(0);
      
      // Count effect sizes in each bin
      dataset.effectSizes.forEach(es => {
        const binIndex = Math.min(Math.floor((es - minEffect) / binWidth), binCount - 1);
        if (binIndex >= 0) bins[binIndex]++;
      });

      // White pins with decreasing opacity for distinction
      const opacity = 0.9 - (index * 0.2);
      return {
        label: dataset.label,
        data: bins,
        backgroundColor: `rgba(255, 255, 255, ${opacity})`,
        borderColor: 'rgba(255, 255, 255, 1)',
        borderWidth: 1,
        borderRadius: 2,
        type: 'bar' as const
      };
    });

    return {
      chartData: {
        labels,
        datasets: chartDatasets
      }
    };
  }, [datasets]);

  // Chart.js configuration matching Shannon/CI Gap style
  const options = useMemo(() => ({
    responsive: true,
    maintainAspectRatio: false,
    layout: {
      padding: {
        top: 20,
        right: 10,
        bottom: 10,
        left: 10
      }
    },
    plugins: {
      legend: {
        display: false
      },
      title: {
        display: true,
        text: `Effect Size Distribution Comparison`,
        color: '#e4e4e7',
        font: { size: 14, weight: 'bold' as const }
      },
      tooltip: {
        backgroundColor: 'rgba(39, 39, 42, 0.95)',
        titleColor: '#e4e4e7',
        bodyColor: '#e4e4e7',
        borderColor: '#71717a',
        borderWidth: 1,
        callbacks: {
          title: (tooltipItems: any) => {
            const item = tooltipItems[0];
            return `Effect size range: ${item.label}`;
          },
          label: (tooltipItem: any) => {
            return `${tooltipItem.dataset.label}: ${tooltipItem.raw} estimates`;
          },
          afterBody: () => [
            '',
            'Larger samples = more precise estimates',
            'Clustering around true effect shows accuracy'
          ]
        }
      }
    },
    scales: {
      x: {
        title: {
          display: true,
          text: 'Effect Size (Cohen\'s d)',
          color: '#a1a1aa',
          font: { size: 12 }
        },
        ticks: {
          color: '#a1a1aa',
          font: { size: 10 },
          display: false
        },
        grid: {
          color: 'rgba(113, 113, 122, 0.3)',
          display: false
        }
      },
      y: {
        title: {
          display: true,
          text: 'Frequency',
          color: '#a1a1aa',
          font: { size: 12 }
        },
        ticks: {
          color: '#a1a1aa',
          font: { size: 10 },
          display: false
        },
        grid: {
          color: 'rgba(113, 113, 122, 0.3)',
          display: false
        }
      }
    },
    elements: {
      bar: {
        borderSkipped: false
      }
    },
    animation: {
      duration: 750,
      easing: 'easeOutQuart' as const
    }
  }), []);

  // Custom plugin for true effect size line matching Shannon style
  const trueEffectLinePlugin = useMemo(() => ({
    id: 'trueEffectLine',
    afterDatasetsDraw: (chart: any) => {
      const { ctx, chartArea, scales } = chart;
      if (!chartArea || !scales.x) return;

      // Calculate position based on data range
      const minEffect = Math.min(...datasets.flatMap(d => d.effectSizes));
      const maxEffect = Math.max(...datasets.flatMap(d => d.effectSizes));
      const range = maxEffect - minEffect;
      
      if (trueEffectSize >= minEffect && trueEffectSize <= maxEffect) {
        const position = (trueEffectSize - minEffect) / range;
        const xPosition = chartArea.left + (position * chartArea.width);

        ctx.save();
        ctx.strokeStyle = '#22d3ee';
        ctx.lineWidth = 2;
        ctx.setLineDash([3, 3]);
        
        ctx.beginPath();
        ctx.moveTo(xPosition, chartArea.top);
        ctx.lineTo(xPosition, chartArea.bottom);
        ctx.stroke();

        // Label
        ctx.fillStyle = '#22d3ee';
        ctx.font = '10px sans-serif';
        ctx.textAlign = 'center';
        ctx.fillText(`True: ${trueEffectSize.toFixed(2)}`, xPosition, chartArea.top - 5);

        ctx.restore();
      }
    }
  }), [trueEffectSize, datasets]);

  return (
    <div className="h-48 overflow-hidden">
      <Bar 
        data={chartData} 
        options={options}
        plugins={[trueEffectLinePlugin]}
      />
    </div>
  );
};

