import React, { useState, useMemo } from 'react';
import { motion, AnimatePresence } from 'motion/react';
import { Line } from 'react-chartjs-2';
import { PValueHistogram } from './PValueHistogram';
import { HorizontalHistogramCarousel } from './HorizontalHistogramCarousel';
import { LayoutGrid, Layers } from 'lucide-react';

interface HistogramDataset {
  pValues: number[];
  sampleSize: number;
  label: string;
}

interface AnimatedHistogramPanelProps {
  datasets: HistogramDataset[];
  title: string;
  significanceLevel?: number;
  initialMode?: 'separate' | 'overlay';
}

export const AnimatedHistogramPanel: React.FC<AnimatedHistogramPanelProps> = ({
  datasets,
  title,
  significanceLevel = 0.05,
  initialMode = 'separate'
}) => {
  const [mode, setMode] = useState<'separate' | 'overlay'>(initialMode);
  
  // Don't render if no datasets or only one dataset
  if (!datasets || datasets.length <= 1) {
    return (
      <PValueHistogram
        pValues={datasets?.[0]?.pValues || []}
        title={title}
        significanceLevel={significanceLevel}
      />
    );
  }

  return (
    <div className="space-y-4">
      {/* Mode Toggle */}
      <div className="flex items-center justify-between">
        <h3 className="text-lg font-semibold text-zinc-100">{title}</h3>
        <div className="flex items-center bg-zinc-800 rounded-lg relative overflow-hidden">
          {/* Animated background */}
          <motion.div
            className="absolute top-0 left-0 w-1/2 h-full bg-red-600"
            animate={{ x: mode === 'separate' ? '0%' : '100%' }}
            transition={{ type: 'spring', stiffness: 300, damping: 30 }}
            layoutId="activeToggle"
          />
          <motion.button
            onClick={() => setMode('separate')}
            className={`relative z-10 flex items-center gap-2 px-3 py-1.5 rounded text-sm font-medium transition-colors ${
              mode === 'separate'
                ? 'text-white'
                : 'text-zinc-400 hover:text-zinc-200'
            }`}
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
          >
            <LayoutGrid className="h-4 w-4" />
            Separate
          </motion.button>
          
          <motion.button
            onClick={() => setMode('overlay')}
            className={`relative z-10 flex items-center gap-2 px-3 py-1.5 rounded text-sm font-medium transition-colors ${
              mode === 'overlay'
                ? 'text-white'
                : 'text-zinc-400 hover:text-zinc-200'
            }`}
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
          >
            <Layers className="h-4 w-4" />
            Overlay
          </motion.button>
        </div>
      </div>

      {/* Animated Container with Motion */}
      <div className="relative overflow-hidden rounded-lg">
        <AnimatePresence mode="wait">
          {mode === 'separate' ? (
            <motion.div
              key="separate"
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: -20 }}
              transition={{ duration: 0.4, ease: "easeInOut" }}
              layout
            >
              {/* Separate Mode: Horizontal Slideshow with Time Scroller */}
              <HorizontalHistogramCarousel
                datasets={datasets}
                significanceLevel={significanceLevel}
              />
            </motion.div>
          ) : (
            <motion.div
              key="overlay"
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: -20 }}
              transition={{ duration: 0.4, ease: "easeInOut" }}
              layout
            >
              {/* Overlay Mode: Single Chart with Multiple Datasets */}
              <motion.div 
                className="bg-zinc-800 rounded-lg p-4 overflow-hidden"
                initial={{ scale: 0.95, opacity: 0 }}
                animate={{ scale: 1, opacity: 1 }}
                transition={{ delay: 0.1, duration: 0.3 }}
                whileHover={{ scale: 1.01 }}
              >
                <OverlappingPValueHistogram
                  datasets={datasets}
                  significanceLevel={significanceLevel}
                />
                
                <motion.div 
                  className="flex justify-center mt-4 space-x-4"
                  initial={{ opacity: 0, y: 10 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ delay: 0.3, duration: 0.3 }}
                >
                  {datasets.map((dataset, index) => (
                    <motion.div 
                      key={`legend-${dataset.sampleSize}`} 
                      className="flex items-center gap-2"
                      initial={{ opacity: 0, x: -10 }}
                      animate={{ opacity: 1, x: 0 }}
                      transition={{ delay: 0.4 + index * 0.05 }}
                      whileHover={{ scale: 1.05 }}
                    >
                      <motion.div 
                        className="w-3 h-3 rounded"
                        style={{ backgroundColor: `rgba(255, 255, 255, ${0.9 - index * 0.2})` }}
                        whileHover={{ scale: 1.2 }}
                      />
                      <span className="text-sm text-zinc-300">{dataset.label}</span>
                    </motion.div>
                  ))}
                </motion.div>
              </motion.div>
            </motion.div>
          )}
        </AnimatePresence>
      </div>

      {/* Educational Context */}
      <div className="bg-zinc-800/50 border border-zinc-700 rounded-lg p-3 text-sm">
        <p className="text-zinc-300">
          <strong className="text-zinc-100">{mode === 'separate' ? 'Separate View:' : 'Overlay View:'}</strong>{' '}
          {mode === 'separate'
            ? 'Compare individual density curves to see how sample size affects the distribution shape and statistical power.'
            : 'Overlay multiple density curves on the same chart. Hover over curves to highlight and compare distributions across different sample sizes.'}
        </p>
      </div>
    </div>
  );
};

// Kernel density estimation for smooth distribution curves
const kernelDensityEstimation = (data: number[], bandwidth: number, points: number): { x: number; y: number }[] => {
  if (data.length === 0) return [];
  
  const result: { x: number; y: number }[] = [];
  const step = 1 / (points - 1);
  
  for (let i = 0; i < points; i++) {
    const x = i * step;
    let density = 0;
    
    // Gaussian kernel density estimation
    for (const value of data) {
      const u = (x - value) / bandwidth;
      density += Math.exp(-0.5 * u * u) / Math.sqrt(2 * Math.PI);
    }
    
    density = density / (data.length * bandwidth);
    result.push({ x, y: density });
  }
  
  return result;
};

// Overlapping density curves using Chart.js with multiple datasets and hover highlighting
const OverlappingPValueHistogram: React.FC<{
  datasets: HistogramDataset[];
  significanceLevel: number;
}> = ({ datasets, significanceLevel }) => {
  const { chartData } = useMemo(() => {
    const densityPoints = 100;
    
    // Create x-axis labels
    const labels = Array(densityPoints).fill(0).map((_, i) => {
      const x = i / (densityPoints - 1);
      return x.toFixed(3);
    });

    // Create datasets for each sample size with normal curve distributions
    const chartDatasets: any[] = [];
    
    // Find the significance threshold index
    const significanceIndex = Math.floor(significanceLevel * densityPoints);
    
    datasets.forEach((dataset, index) => {
      // Calculate optimal bandwidth for this dataset
      const bandwidth = Math.pow(4 / (3 * dataset.pValues.length), 1/5) * Math.sqrt(
        dataset.pValues.reduce((sum, p) => sum + p * p, 0) / dataset.pValues.length
      );
      
      // Get density estimation
      const densityData = kernelDensityEstimation(dataset.pValues, bandwidth, densityPoints);
      const densityValues = densityData.map(d => d.y);
      
      const opacity = 0.9 - (index * 0.15);
      
      // Split data into significant (red) and non-significant (white) portions
      const significantData = densityValues.map((value, i) => 
        i <= significanceIndex ? value : null
      );
      const nonSignificantData = densityValues.map((value, i) => 
        i >= significanceIndex ? value : null
      );
      
      // Significant region (red)
      chartDatasets.push({
        label: `${dataset.label} (significant)`,
        data: significantData,
        backgroundColor: `rgba(239, 68, 68, ${opacity * 0.25})`,
        borderColor: `rgba(239, 68, 68, ${opacity})`,
        borderWidth: 3,
        fill: 'origin',
        tension: 0.4,
        pointRadius: 0,
        pointHoverRadius: 0,
        spanGaps: false,
        // Hover highlighting
        hoverBackgroundColor: `rgba(239, 68, 68, ${opacity * 0.5})`,
        hoverBorderColor: `rgba(239, 68, 68, 1)`,
        hoverBorderWidth: 4
      });
      
      // Non-significant region (white)
      chartDatasets.push({
        label: `${dataset.label} (non-significant)`,
        data: nonSignificantData,
        backgroundColor: `rgba(255, 255, 255, ${opacity * 0.25})`,
        borderColor: `rgba(255, 255, 255, ${opacity})`,
        borderWidth: 3,
        fill: 'origin',
        tension: 0.4,
        pointRadius: 0,
        pointHoverRadius: 0,
        spanGaps: false,
        // Hover highlighting
        hoverBackgroundColor: `rgba(255, 255, 255, ${opacity * 0.5})`,
        hoverBorderColor: `rgba(255, 255, 255, 1)`,
        hoverBorderWidth: 4
      });
    });

    return {
      chartData: {
        labels,
        datasets: chartDatasets
      }
    };
  }, [datasets, significanceLevel]);

  // Chart.js configuration for overlapping density curves with interactive highlighting
  const options = useMemo(() => ({
    responsive: true,
    maintainAspectRatio: false,
    layout: {
      padding: {
        top: 20,
        right: 10,
        bottom: 10,
        left: 10
      }
    },
    plugins: {
      legend: {
        display: false
      },
      title: {
        display: true,
        text: `P-Value Distribution Comparison`,
        color: '#e4e4e7',
        font: { size: 14, weight: 'normal' as const, family: 'Roboto, sans-serif' }
      },
              tooltip: {
          backgroundColor: 'rgba(39, 39, 42, 0.95)',
          titleColor: '#e4e4e7',
          bodyColor: '#e4e4e7',
          borderColor: '#71717a',
          borderWidth: 1,
          filter: (tooltipItem: any) => tooltipItem.raw !== null, // Only show for non-null values
          callbacks: {
            title: (tooltipItems: any) => {
              const item = tooltipItems[0];
              const pValue = parseFloat(item.label);
              return `P-value: ${pValue.toFixed(3)}`;
            },
            label: (tooltipItem: any) => {
              const density = tooltipItem.raw as number;
              const fullLabel = tooltipItem.dataset.label;
              // Extract sample size and region from label like "n = 20 (significant)" 
              const sampleSize = fullLabel.split(' (')[0];
              const isSignificant = fullLabel.includes('(significant)');
              return `${sampleSize}: density ${density.toFixed(4)} ${isSignificant ? '(significant)' : '(not significant)'}`;
            },
            afterBody: () => [
              '',
              'Red curves: statistically significant regions (p < α)',
              'White curves: non-significant regions (p ≥ α)',
              'Hover to highlight individual sample sizes'
            ]
          }
        }
    },
    scales: {
      x: {
        type: 'linear' as const,
        min: 0,
        max: 1,
        title: {
          display: true,
          text: 'P-Value',
          color: '#a1a1aa',
          font: { size: 12 }
        },
        ticks: {
          color: '#a1a1aa',
          font: { size: 10 },
          stepSize: 0.1
        },
        grid: {
          color: 'rgba(113, 113, 122, 0.3)'
        }
      },
      y: {
        min: 0,
        title: {
          display: true,
          text: 'Density',
          color: '#a1a1aa',
          font: { size: 12 }
        },
        ticks: {
          color: '#a1a1aa',
          font: { size: 10 }
        },
        grid: {
          color: 'rgba(113, 113, 122, 0.3)'
        }
      }
    },
    elements: {
      line: {
        borderJoinStyle: 'round' as const,
        borderCapStyle: 'round' as const
      }
    },
    animation: {
      duration: 750,
      easing: 'easeOutQuart' as const
    },
    interaction: {
      intersect: false,
      mode: 'index' as const, // Highlight datasets at same x position
    },
    onHover: (event: any, activeElements: any[], chart: any) => {
      // Change cursor on hover
      const canvas = event.native.target;
      canvas.style.cursor = activeElements.length > 0 ? 'pointer' : 'default';
      
      // Custom hover logic to highlight both significant and non-significant parts of same sample size
      if (activeElements.length > 0) {
        const hoveredIndex = activeElements[0].datasetIndex;
        const sampleSizeIndex = Math.floor(hoveredIndex / 2); // Each sample size has 2 datasets
        
        // Reset all datasets
        chart.data.datasets.forEach((dataset: any, index: number) => {
          const currentSampleSizeIndex = Math.floor(index / 2);
          if (currentSampleSizeIndex === sampleSizeIndex) {
            // Highlight this sample size's datasets
            // const opacity = 0.9 - (currentSampleSizeIndex * 0.15); // Unused
            const isSignificant = index % 2 === 0;
            dataset.borderColor = isSignificant 
              ? `rgba(239, 68, 68, 1)` 
              : `rgba(255, 255, 255, 1)`;
            dataset.borderWidth = 4;
          } else {
            // Dim other sample sizes
            const opacity = 0.9 - (currentSampleSizeIndex * 0.15);
            const isSignificant = index % 2 === 0;
            dataset.borderColor = isSignificant 
              ? `rgba(239, 68, 68, ${opacity * 0.5})` 
              : `rgba(255, 255, 255, ${opacity * 0.5})`;
            dataset.borderWidth = 2;
          }
        });
        chart.update('none');
      }
    }
  }), []);

  // Custom plugin for significance line
  const significanceLinePlugin = useMemo(() => ({
    id: 'significanceLine',
    afterDatasetsDraw: (chart: any) => {
      const { ctx, chartArea, scales } = chart;
      if (!chartArea || !scales.x) return;

      const xPosition = chartArea.left + (chartArea.width * significanceLevel);

      ctx.save();
      ctx.strokeStyle = '#ef4444';
      ctx.lineWidth = 2;
      ctx.setLineDash([5, 5]);
      
      ctx.beginPath();
      ctx.moveTo(xPosition, chartArea.top);
      ctx.lineTo(xPosition, chartArea.bottom);
      ctx.stroke();

      // Label
      ctx.fillStyle = '#ef4444';
      ctx.font = '10px sans-serif';
      ctx.textAlign = 'center';
      ctx.fillText(`α = ${significanceLevel}`, xPosition, chartArea.top - 5);

      ctx.restore();
    }
  }), [significanceLevel]);

  return (
    <div className="h-64 overflow-hidden">
      <Line 
        data={chartData} 
        options={options}
        plugins={[significanceLinePlugin]}
      />
    </div>
  );
};

