import React, { useMemo } from 'react';
import { Card } from '../ui/Card';

interface PowerAnalysisData {
  sampleSize: number;
  label: string;
  pValues: number[];
}

interface StatisticalPowerTableProps {
  datasets: PowerAnalysisData[];
  significanceLevels?: number[];
  title?: string;
}

export const StatisticalPowerTable: React.FC<StatisticalPowerTableProps> = ({
  datasets,
  significanceLevels = [0.5, 0.1, 0.05, 0.01, 0.001],
  title = "Statistical Power Analysis"
}) => {
  const powerData = useMemo(() => {
    return datasets.map(dataset => {
      const powerDetails = significanceLevels.map(alpha => {
        const significantCount = dataset.pValues.filter(p => p < alpha).length;
        const power = significantCount / dataset.pValues.length;
        return {
          power,
          significantCount,
          totalCount: dataset.pValues.length
        };
      });
      
      return {
        sampleSize: dataset.sampleSize,
        label: dataset.label,
        totalTests: dataset.pValues.length,
        powerDetails
      };
    });
  }, [datasets, significanceLevels]);

  const formatPower = (power: number) => {
    return (power * 100).toFixed(1) + '%';
  };

  const getPowerColor = (power: number) => {
    if (power >= 0.8) return 'text-green-400'; // Good power
    if (power >= 0.5) return 'text-yellow-400'; // Moderate power
    return 'text-red-400'; // Low power
  };

  const getAlphaLabel = (alpha: number) => {
    return `α = ${alpha}`;
  };

  return (
    <Card className="p-6">
      <h3 className="text-lg font-semibold text-zinc-100 mb-4">{title}</h3>
      
      <div className="overflow-x-auto">
        <table className="w-full text-sm">
          <thead>
            <tr className="border-b border-zinc-700">
              <th className="text-left py-2 px-3 text-zinc-300 font-medium">
                Sample Size
              </th>
              {significanceLevels.map(alpha => (
                <th key={alpha} className="text-center py-2 px-3 text-zinc-300 font-medium">
                  {getAlphaLabel(alpha)}
                </th>
              ))}
              <th className="text-center py-2 px-3 text-zinc-300 font-medium">
                Tests
              </th>
            </tr>
          </thead>
          <tbody>
            {powerData.map((row, index) => (
              <tr key={index} className="border-b border-zinc-800">
                <td className="py-3 px-3 text-zinc-100 font-medium">
                  {row.label}
                </td>
                {row.powerDetails.map((detail, powerIndex) => (
                  <td key={powerIndex} className="text-center py-3 px-3">
                    <div className={getPowerColor(detail.power)}>
                      {formatPower(detail.power)}
                    </div>
                    <div className="text-xs text-zinc-500">
                      {detail.significantCount}/{detail.totalCount}
                    </div>
                  </td>
                ))}
                <td className="text-center py-3 px-3 text-zinc-400">
                  {row.totalTests.toLocaleString()}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {/* Power interpretation legend */}
      <div className="mt-4 pt-3 border-t border-zinc-700">
        <div className="text-xs text-zinc-500 mb-2">Power Interpretation:</div>
        <div className="flex flex-wrap gap-4 text-xs">
          <span className="flex items-center gap-1">
            <div className="w-2 h-2 bg-green-400 rounded"></div>
            <span className="text-zinc-400">≥80% (Good)</span>
          </span>
          <span className="flex items-center gap-1">
            <div className="w-2 h-2 bg-yellow-400 rounded"></div>
            <span className="text-zinc-400">50-79% (Moderate)</span>
          </span>
          <span className="flex items-center gap-1">
            <div className="w-2 h-2 bg-red-400 rounded"></div>
            <span className="text-zinc-400">&lt;50% (Low)</span>
          </span>
        </div>
      </div>

      {/* Statistical note */}
      <div className="mt-3 text-xs text-zinc-500 italic">
        Power = Probability of detecting a true effect when it exists (1 - β, where β is Type II error rate)
      </div>
    </Card>
  );
}; 