import { useRef, useCallback, useEffect, useState } from 'react';
import { useAppStore } from '../store/index';
import { useDesktop } from './useDesktop';
import type { FlexibleSimulationConfig, SimulationResults } from '../types/index';

// Types matching the Web Worker interfaces
interface SimulationConfig {
  id: string;
  samplePairs: SamplePair[];
  simulationCount: number;
  significanceThresholds: number[];
  confidenceLevel: number;
}

interface SamplePair {
  id: string;
  name: string;
  population1: PopulationConfig;
  population2: PopulationConfig;
  sampleSize: number;
}

interface PopulationConfig {
  mean: number;
  std: number;
  size: number;
}

interface WorkerMessage {
  type: 'SIMULATION_START' | 'SIMULATION_PROGRESS' | 'SIMULATION_COMPLETE' | 'SIMULATION_ERROR' | 'TEST' | 'TEST_RESPONSE';
  id: string;
  data?: any;
  progress?: number;
  error?: string;
}

type SimulationStatus = 'idle' | 'running' | 'completed' | 'error' | 'cancelled';

interface SimulationState {
  status: SimulationStatus;
  progress: number;
  results: SimulationResults | null;
  error: string | null;
  completedSimulations: number;
}

interface UseSimulationWorkerReturn {
  // State
  simulationState: SimulationState;
  isRunning: boolean;
  
  // Actions
  runSimulation: (config: FlexibleSimulationConfig) => Promise<SimulationResults>;
  cancelSimulation: () => void;
  resetState: () => void;
  
  // Convenience methods
  createLegacyNotebookConfig: (
    mean1?: number, 
    mean2?: number, 
    std1?: number, 
    std2?: number,
    sampleSize?: number,
    simulationCount?: number
  ) => SimulationConfig;
  
  createNullHypothesisConfig: (
    mean?: number,
    std?: number,
    sampleSize?: number,
    simulationCount?: number
  ) => SimulationConfig;
}

// Helper function to convert FlexibleSimulationConfig to worker format
const convertConfigForWorker = (config: FlexibleSimulationConfig) => {
  return {
    id: config.id,
    samplePairs: config.samplePairs.map(pair => ({
      id: pair.id,
      name: pair.name,
      population1: pair.population1,
      population2: pair.population2,
      sampleSize: pair.sampleSize
    })),
    simulationCount: config.simulationCount,
    significanceThresholds: config.significanceThresholds,
    confidenceLevel: config.confidenceLevel
  };
};

export const useSimulationWorker = (): UseSimulationWorkerReturn => {
  const workerRef = useRef<Worker | null>(null);
  const currentSimulationId = useRef<string | null>(null);
  const simulationStartTime = useRef<number | null>(null);
  const promiseResolvers = useRef<Map<string, {
    resolve: (results: SimulationResults) => void;
    reject: (error: Error) => void;
  }>>(new Map());

  // Get store actions
  const { setProgress, setResults, clearResults } = useAppStore();
  
  // Get desktop notifications
  const { notifications } = useDesktop();

  // Simulation state
  const [simulationState, setSimulationState] = useState<SimulationState>({
    status: 'idle',
    progress: 0,
    results: null,
    error: null,
    completedSimulations: 0
  });

  // Shared message handler setup function
  const setupWorkerMessageHandler = useCallback((worker: Worker) => {
    // Set up message handler
    worker.onmessage = (e: MessageEvent<WorkerMessage>) => {
      const { type, id, data, progress, error } = e.data;
      
      switch (type) {
        case 'TEST_RESPONSE':
          // Worker communication test successful
          break;
          
        case 'SIMULATION_PROGRESS':
          const progressValue = progress || 0;
          const completedSims = data?.completedSimulations || 0;
          
          setSimulationState(prev => ({
            ...prev,
            progress: progressValue,
            completedSimulations: completedSims
          }));
          
          // Update store progress
          setProgress({
            isRunning: true,
            progress: progressValue
          });
          break;

        case 'SIMULATION_COMPLETE':
          const resolver = promiseResolvers.current.get(id);
          if (resolver) {
            resolver.resolve(data as SimulationResults);
            promiseResolvers.current.delete(id);
          }
          
          setSimulationState(prev => ({
            ...prev,
            status: 'completed',
            progress: 1,
            results: data as SimulationResults
          }));
          
          // Update store with results and clear progress
          setResults(data as SimulationResults);
          setProgress({
            isRunning: false,
            progress: 1
          });
          
          // Send desktop notification for simulation completion
          if (simulationStartTime.current) {
            const duration = Date.now() - simulationStartTime.current;
            const configName = (data as any)?.configName || 'Simulation';
            notifications.sendSimulationComplete(configName, duration).catch(console.error);
          }
          
          currentSimulationId.current = null;
          simulationStartTime.current = null;
          break;

        case 'SIMULATION_ERROR':
          const errorResolver = promiseResolvers.current.get(id);
          if (errorResolver) {
            errorResolver.reject(new Error(error || 'Unknown simulation error'));
            promiseResolvers.current.delete(id);
          }
          
          setSimulationState(prev => ({
            ...prev,
            status: 'error',
            error: error || 'Unknown simulation error'
          }));
          
          // Clear progress on error
          setProgress({
            isRunning: false,
            progress: 0
          });
          
          // Send desktop notification for simulation error
          notifications.send('Simulation Failed', error || 'Unknown simulation error').catch(console.error);
          
          currentSimulationId.current = null;
          simulationStartTime.current = null;
          break;
      }
    };

    // Handle worker errors
    worker.onerror = (error) => {
      console.error('Worker error:', error);
      setSimulationState(prev => ({
        ...prev,
        status: 'error',
        error: 'Worker execution error'
      }));
      
      setProgress({
        isRunning: false,
        progress: 0
      });
      
      // Send desktop notification for worker error
      notifications.send('Simulation Error', 'Worker execution error').catch(console.error);
      
      simulationStartTime.current = null;
    };

    // Test worker communication
    worker.postMessage({ type: 'TEST', id: 'test-communication' });
  }, []); // Remove all dependencies to prevent recreation

  // Initialize worker
  useEffect(() => {
    // Create worker instance
    workerRef.current = new Worker(
      new URL('../workers/simulation.worker.ts', import.meta.url),
      { type: 'module' }
    );

    // Set up message handler using shared function
    setupWorkerMessageHandler(workerRef.current);

    return () => {
      if (workerRef.current) {
        workerRef.current.terminate();
      }
    };
  }, [setupWorkerMessageHandler]);

  // Run simulation
  const runSimulation = useCallback(async (config: FlexibleSimulationConfig): Promise<SimulationResults> => {
    if (!workerRef.current) {
      throw new Error('Worker not initialized');
    }

    if (simulationState.status === 'running') {
      throw new Error('Simulation already running. Cancel current simulation first.');
    }

    // Generate unique ID for this simulation
    const simulationId = `sim-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`;
    currentSimulationId.current = simulationId;
    simulationStartTime.current = Date.now();

    // Reset state for new simulation
    setSimulationState({
      status: 'running',
      progress: 0,
      results: null,
      error: null,
      completedSimulations: 0
    });

    // Clear previous results and set progress
    clearResults();
    setProgress({
      isRunning: true,
      progress: 0
    });

    // Convert config to worker format and include config name for notifications
    const workerConfig = {
      ...convertConfigForWorker(config),
      configName: config.name
    };

    // Create promise for result
    return new Promise<SimulationResults>((resolve, reject) => {
      promiseResolvers.current.set(simulationId, { resolve, reject });
      
      // Start simulation
      workerRef.current!.postMessage({
        type: 'SIMULATION_START',
        id: simulationId,
        data: workerConfig
      });

      // Set timeout for very long simulations (5 minutes)
      setTimeout(() => {
        if (currentSimulationId.current === simulationId) {
          console.log('Simulation timeout reached');
          const resolver = promiseResolvers.current.get(simulationId);
          if (resolver) {
            resolver.reject(new Error('Simulation timeout'));
            promiseResolvers.current.delete(simulationId);
          }
          
          setSimulationState(prev => ({
            ...prev,
            status: 'error',
            error: 'Simulation timeout'
          }));
          
          setProgress({
            isRunning: false,
            progress: 0
          });
          
          // Send timeout notification
          notifications.send('Simulation Timeout', 'Simulation exceeded 5 minute limit').catch(console.error);
          
          currentSimulationId.current = null;
          simulationStartTime.current = null;
        }
      }, 300000); // 5 minutes
    });
  }, [simulationState.status, clearResults, setProgress, notifications]);

  // Cancel simulation
  const cancelSimulation = useCallback(() => {
    if (currentSimulationId.current) {
      // Reject any pending promise
      const resolver = promiseResolvers.current.get(currentSimulationId.current);
      if (resolver) {
        resolver.reject(new Error('Simulation cancelled'));
        promiseResolvers.current.delete(currentSimulationId.current);
      }
      
      currentSimulationId.current = null;
      simulationStartTime.current = null;
    }

    setSimulationState(prev => ({
      ...prev,
      status: 'cancelled',
      error: null
    }));
    
    setProgress({
      isRunning: false,
      progress: 0
    });

    // Terminate and recreate worker to stop any ongoing work
    if (workerRef.current) {
      workerRef.current.terminate();
      
      // Recreate worker with message handler
      workerRef.current = new Worker(
        new URL('../workers/simulation.worker.ts', import.meta.url),
        { type: 'module' }
      );
      
      // Use shared message handler setup
      setupWorkerMessageHandler(workerRef.current);
    }
  }, [setupWorkerMessageHandler]);

  // Reset state
  const resetState = useCallback(() => {
    setSimulationState({
      status: 'idle',
      progress: 0,
      results: null,
      error: null,
      completedSimulations: 0
    });
    
    clearResults();
    setProgress({
      isRunning: false,
      progress: 0
    });
  }, [clearResults, setProgress]);

  // Create legacy notebook configuration (reproduces original notebook)
  const createLegacyNotebookConfig = useCallback((
    mean1: number = 100,
    mean2: number = 110,
    std1: number = 15,
    std2: number = 15,
    sampleSize: number = 30,
    simulationCount: number = 1000
  ): SimulationConfig => {
    return {
      id: `legacy-${Date.now()}`,
      samplePairs: [{
        id: 'legacy-pair',
        name: 'Legacy Notebook Reproduction',
        population1: {
          mean: mean1,
          std: std1,
          size: 1000
        },
        population2: {
          mean: mean2,
          std: std2,
          size: 1000
        },
        sampleSize
      }],
      simulationCount,
      significanceThresholds: [0.5, 0.1, 0.05, 0.01, 0.001],
      confidenceLevel: 0.95
    };
  }, []);

  // Create null hypothesis configuration (no difference)
  const createNullHypothesisConfig = useCallback((
    mean: number = 100,
    std: number = 15,
    sampleSize: number = 30,
    simulationCount: number = 1000
  ): SimulationConfig => {
    return {
      id: `null-${Date.now()}`,
      samplePairs: [{
        id: 'null-pair',
        name: 'Null Hypothesis Test',
        population1: {
          mean,
          std,
          size: 1000
        },
        population2: {
          mean, // Same mean = null hypothesis
          std,
          size: 1000
        },
        sampleSize
      }],
      simulationCount,
      significanceThresholds: [0.5, 0.1, 0.05, 0.01, 0.001],
      confidenceLevel: 0.95
    };
  }, []);

  return {
    // State
    simulationState,
    isRunning: simulationState.status === 'running',
    
    // Actions
    runSimulation,
    cancelSimulation,
    resetState,
    
    // Convenience methods
    createLegacyNotebookConfig,
    createNullHypothesisConfig
  };
}; 