import React, { useMemo } from 'react';
import type { SimulationResults as SimulationResultsType } from '../types/index';
import { 
  PValueHistogram, 
  EffectSizeDistribution, 
  StatisticalPowerTable, 
  AnimatedHistogramPanel, 
  AnimatedEffectSizePanel,
  ShannonSValuesChart,
  CIGapChart
} from './charts';
import { 
  usePValueHistogramVisible, 
  useEffectSizeDistributionVisible, 
  useStatisticalPowerTableVisible,
  useShannonSValuesVisible,
  useCiGapChartVisible,
  usePValueHistogramMode,
  useEffectSizeDistributionMode
} from '../stores/chartsStore';

import { BookOpen } from 'lucide-react';

interface SimulationResultsProps {
  results: SimulationResultsType;
}

export const SimulationResults: React.FC<SimulationResultsProps> = ({ results }) => {
  // Get chart visibility and mode state from store (individual selectors to avoid object creation)
  const pValueHistogramVisible = usePValueHistogramVisible();
  const effectSizeDistributionVisible = useEffectSizeDistributionVisible();
  const statisticalPowerTableVisible = useStatisticalPowerTableVisible();
  const shannonSValuesVisible = useShannonSValuesVisible();
  const ciGapChartVisible = useCiGapChartVisible();
  const pValueHistogramMode = usePValueHistogramMode();
  const effectSizeDistributionMode = useEffectSizeDistributionMode();
  
  // Don't automatically update store - let the parent component handle this
  
  // Prepare data for the scientific visualizations
  const visualizationData = useMemo(() => {
    // Check if we have multi-pair results (like the original notebook)
    const hasMultiplePairs = results.samplePairResults && results.samplePairResults.length > 1;
    
    if (hasMultiplePairs) {
      // Multi-pair comparative analysis (like the original notebook)
      return {
        // P-value histograms for comparison (each sample size)
        pValues: results.pValues, // Keep legacy for single histogram
        pValueDatasets: results.samplePairResults.map(pair => ({
          pValues: pair.pValues,
          sampleSize: pair.sampleSize,
          label: `n = ${pair.sampleSize}`
        })),
        
        // Effect size distributions for comparison
        effectSizeDatasets: results.samplePairResults.map(pair => ({
          effectSizes: pair.effectSize,
          sampleSize: pair.sampleSize,
          label: `n = ${pair.sampleSize}`
        })),
        
        // Power analysis for all sample sizes
        powerDatasets: results.samplePairResults.map(pair => ({
          sampleSize: pair.sampleSize,
          label: `n = ${pair.sampleSize}`,
          pValues: pair.pValues
        })),
        
        // Use the true effect size from first pair (should be consistent)
        trueEffectSize: results.samplePairResults[0]?.trueEffectSize || 0.5,
        isMultiPair: true
      };
    } else {
      // Single pair (legacy compatibility)
      const sampleSize = results.sampleSize || 30;
      const sampleSizeLabel = `n = ${sampleSize}`;
      
      return {
        // P-value histogram data
        pValues: results.pValues,
        pValueDatasets: [{
          pValues: results.pValues,
          sampleSize: sampleSize,
          label: sampleSizeLabel
        }],
        
        // Effect size distribution data  
        effectSizeDatasets: [{
          effectSizes: results.effectSize,
          sampleSize: sampleSize,
          label: sampleSizeLabel
        }],
        
        // Power analysis data
        powerDatasets: [{
          sampleSize: sampleSize,
          label: sampleSizeLabel,
          pValues: results.pValues
        }],
        
        // True effect size (calculated from simulation parameters)
        trueEffectSize: results.trueEffectSize || 0.5,
        isMultiPair: false
      };
    }
  }, [results]);

  // const formatNumber = (num: number, decimals: number = 3) => num.toFixed(decimals); // Unused

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center gap-2 mb-6">
        <BookOpen className="h-6 w-6 text-blue-400" />
        <h2 className="text-xl font-semibold text-zinc-100">
          {visualizationData.isMultiPair ? "Comparative Statistical Analysis" : "Statistical Analysis Results"}
        </h2>
        <div className="ml-auto text-sm text-zinc-400">
          {visualizationData.isMultiPair 
            ? `${results.samplePairResults.length} sample sizes × ${results.samplePairResults[0]?.effectSize.length || 0} simulations each`
            : `${results.effectSize.length.toLocaleString()} simulations completed`
          }
        </div>
      </div>



      {/* Core Scientific Visualizations - Vertical Layout */}
      <div className="space-y-8">
        {/* P-Value Distribution - The key insight from the notebook */}
        {pValueHistogramVisible && (
          <div>
            {visualizationData.isMultiPair ? (
              <AnimatedHistogramPanel
                datasets={visualizationData.pValueDatasets}
                title="P-Value Distribution Comparison"
                significanceLevel={0.05}
                initialMode={pValueHistogramMode}
              />
            ) : (
              <PValueHistogram
                pValues={visualizationData.pValues}
                title="P-Value Distribution"
                significanceLevel={0.05}
              />
            )}
          </div>
        )}

        {/* Effect Size Distribution - Compare precision across sample sizes */}
        {effectSizeDistributionVisible && (
          <div>
            {visualizationData.isMultiPair ? (
              <AnimatedEffectSizePanel
                datasets={visualizationData.effectSizeDatasets}
                title="Effect Size Precision Comparison"
                trueEffectSize={visualizationData.trueEffectSize}
                initialMode={effectSizeDistributionMode}
              />
            ) : (
              <EffectSizeDistribution
                datasets={visualizationData.effectSizeDatasets}
                title="Effect Size Distribution"
                trueEffectSize={visualizationData.trueEffectSize}
              />
            )}
          </div>
        )}

        {/* Shannon S-Values Chart - Information against null hypothesis */}
        {shannonSValuesVisible && (
          <ShannonSValuesChart
            sValues={results.sValues}
            sampleSize={results.sampleSize}
          />
        )}

        {/* CI Gap Chart - Precision visualization */}
        {ciGapChartVisible && (
          <CIGapChart
            ciGaps={results.ciGaps}
            sampleSize={results.sampleSize}
            confidenceLevel={0.95}
          />
        )}
      </div>

      {/* Statistical Power Analysis Table */}
      {statisticalPowerTableVisible && (
        <StatisticalPowerTable
          datasets={visualizationData.powerDatasets}
          title="Statistical Power Analysis"
        />
      )}

      {/* Educational Context */}
      <div className="bg-blue-900/20 border border-blue-800/30 rounded-lg p-4 mt-6">
        <h4 className="text-lg font-semibold text-blue-300 mb-2">Understanding Your Results</h4>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4 text-sm text-blue-100">
          <div>
            <h5 className="font-medium mb-1">P-Value Distribution:</h5>
            <p className="text-blue-200/80">
              Shows how often your statistical tests would detect the effect. 
              When a true effect exists, most p-values should be below your significance threshold.
            </p>
          </div>
          <div>
            <h5 className="font-medium mb-1">Effect Size Distribution:</h5>
            <p className="text-blue-200/80">
              Demonstrates the precision of your effect size estimates. 
              Larger sample sizes produce more precise estimates clustered around the true value.
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}; 