import React from 'react';
import { ResizablePanelGroup, ResizablePanel, ResizableHandle } from '../ui/Resizable';
import { useUIStore } from '../../stores/uiStore';
import { PanelLeftOpen, PanelRightOpen } from 'lucide-react';
import { Button } from '../ui/Button';

// Import the three main sections
import { LeftSidebar } from './LeftSidebar.tsx';
import { CentralWorkspace } from './CentralWorkspace.tsx';
import { RightSidebar } from './RightSidebar.tsx';

interface AppShellProps {
  children?: React.ReactNode;
}

export const AppShell: React.FC<AppShellProps> = () => {
  const leftSidebarOpen = useUIStore(state => state.leftSidebarOpen);
  const rightSidebarOpen = useUIStore(state => state.rightSidebarOpen);
  const toggleLeftSidebar = useUIStore(state => state.toggleLeftSidebar);
  const toggleRightSidebar = useUIStore(state => state.toggleRightSidebar);
  
  return (
    <div className="h-screen bg-background text-foreground overflow-hidden relative">
      {/* Floating Left Sidebar Toggle */}
      {!leftSidebarOpen && (
        <Button
          variant="ghost"
          size="sm"
          onClick={toggleLeftSidebar}
          className="absolute top-4 left-4 z-50 h-10 w-10 p-0 bg-zinc-800 hover:bg-zinc-700 border border-zinc-600 shadow-lg"
          title="Show Analysis Explorer"
        >
          <PanelLeftOpen className="h-5 w-5 text-zinc-200" />
        </Button>
      )}
      
      {/* Floating Right Sidebar Toggle */}
      {!rightSidebarOpen && (
        <Button
          variant="ghost"
          size="sm"
          onClick={toggleRightSidebar}
          className="absolute top-4 right-4 z-50 h-10 w-10 p-0 bg-zinc-800 hover:bg-zinc-700 border border-zinc-600 shadow-lg"
          title="Show Tools Panel"
        >
          <PanelRightOpen className="h-5 w-5 text-zinc-200" />
        </Button>
      )}
      
      <ResizablePanelGroup direction="horizontal" className="h-full">
        {/* Left Sidebar - Analysis Explorer */}
        {leftSidebarOpen && (
          <>
            <ResizablePanel 
              id="left-sidebar"
              order={1}
              defaultSize={16} 
              minSize={12} 
              maxSize={25}
              className="min-w-0"
              collapsible={false}
            >
              <LeftSidebar />
            </ResizablePanel>
            <ResizableHandle withHandle className="w-1 bg-zinc-700 hover:bg-zinc-600 transition-colors" />
          </>
        )}
        
        {/* Central Workspace */}
        <ResizablePanel 
          id="central-workspace"
          order={2}
          defaultSize={leftSidebarOpen && rightSidebarOpen ? 60 : rightSidebarOpen ? 76 : leftSidebarOpen ? 84 : 100}
          minSize={40}
          className="min-w-0"
        >
          <CentralWorkspace />
        </ResizablePanel>
        
        {/* Right Sidebar - Tools */}
        {rightSidebarOpen && (
          <>
            <ResizableHandle withHandle className="w-1 bg-zinc-700 hover:bg-zinc-600 transition-colors" />
            <ResizablePanel 
              id="right-sidebar"
              order={3}
              defaultSize={24} 
              minSize={20} 
              maxSize={35}
              className="min-w-0"
              collapsible={false}
            >
              <RightSidebar />
            </ResizablePanel>
          </>
        )}
      </ResizablePanelGroup>
    </div>
  );
}; 