import { create } from 'zustand';
import { devtools, persist } from 'zustand/middleware';
import { immer } from 'zustand/middleware/immer';
import type { 
  FlexibleSimulationConfig, 
  ConfigurationTemplate, 
  UIState, 
  SimulationProgress,
  ValidationResult,
  SimulationResults
} from '../types/index.js';

// State interface for the main store
interface AppState {
  // Current simulation configuration
  currentConfig: FlexibleSimulationConfig | null;
  
  // Template management
  templates: ConfigurationTemplate[];
  activeTemplate: ConfigurationTemplate | null;
  
  // UI state
  ui: UIState;
  
  // Validation state
  validation: ValidationResult | null;
  
  // Results state
  results: SimulationResults | null;
  
  // Actions
  setCurrentConfig: (config: FlexibleSimulationConfig) => void;
  updateConfig: (updates: Partial<FlexibleSimulationConfig>) => void;
  clearConfig: () => void;
  
  loadTemplate: (template: ConfigurationTemplate) => void;
  saveAsTemplate: (name: string, description: string) => void;
  deleteTemplate: (id: string) => void;
  
  setUIState: (updates: Partial<UIState>) => void;
  setProgress: (progress: Partial<SimulationProgress>) => void;
  
  setValidation: (validation: ValidationResult) => void;
  clearValidation: () => void;
  
  setResults: (results: SimulationResults) => void;
  clearResults: () => void;
}

// Default UI state
const defaultUIState: UIState = {
  theme: 'dark',
  sidebarOpen: false,
  activeModal: null,
  progress: {
    isRunning: false,
    progress: 0
  }
};

// Create the main Zustand store
export const useAppStore = create<AppState>()(
  devtools(
    persist(
      immer((set) => ({
        // Initial state
        currentConfig: null,
        templates: [],
        activeTemplate: null,
        ui: defaultUIState,
        validation: null,
        results: null,
        
        // Configuration actions
        setCurrentConfig: (config) => 
          set((state) => {
            state.currentConfig = config;
            state.validation = null;
          }),
          
        updateConfig: (updates) =>
          set((state) => {
            if (state.currentConfig) {
              Object.assign(state.currentConfig, updates);
              state.validation = null;
            }
          }),
          
        clearConfig: () =>
          set((state) => {
            state.currentConfig = null;
            state.activeTemplate = null;
            state.validation = null;
          }),
        
        // Template actions
        loadTemplate: (template) =>
          set((state) => {
            state.currentConfig = { ...template.config };
            state.activeTemplate = template;
            state.validation = null;
          }),
          
        saveAsTemplate: (name, description) =>
          set((state) => {
            const { currentConfig } = state;
            if (currentConfig) {
              const newTemplate: ConfigurationTemplate = {
                id: crypto.randomUUID(),
                name,
                description,
                config: { ...currentConfig },
                isBuiltIn: false
              };
              state.templates.push(newTemplate);
            }
          }),
          
        deleteTemplate: (id) =>
          set((state) => {
            state.templates = state.templates.filter((t: ConfigurationTemplate) => t.id !== id);
            if (state.activeTemplate?.id === id) {
              state.activeTemplate = null;
            }
          }),
        
        // UI actions
        setUIState: (updates) =>
          set((state) => {
            Object.assign(state.ui, updates);
          }),
          
        setProgress: (progress) =>
          set((state) => {
            if (!state.ui.progress) {
              state.ui.progress = { isRunning: false, progress: 0 };
            }
            Object.assign(state.ui.progress, progress);
          }),
        
        // Validation actions
        setValidation: (validation) =>
          set((state) => {
            state.validation = validation;
          }),
          
        clearValidation: () =>
          set((state) => {
            state.validation = null;
          }),
        
        // Results actions
        setResults: (results) =>
          set((state) => {
            state.results = results;
          }),
          
        clearResults: () =>
          set((state) => {
            state.results = null;
          })
      })),
      {
        name: 'statdash-storage',
        partialize: (state) => ({
          templates: state.templates.filter((t: ConfigurationTemplate) => !t.isBuiltIn),
          ui: {
            theme: state.ui.theme,
            sidebarOpen: state.ui.sidebarOpen
          }
        })
      }
    ),
    { name: 'StatDash Store' }
  )
);

// Cached fallback objects to prevent infinite loops
const DEFAULT_PROGRESS = { isRunning: false, progress: 0 };

// Selector hooks for better performance
export const useCurrentConfig = () => useAppStore(state => state.currentConfig);
export const useTemplates = () => useAppStore(state => state.templates);
export const useActiveTemplate = () => useAppStore(state => state.activeTemplate);
export const useUIState = () => useAppStore(state => state.ui);
export const useValidation = () => useAppStore(state => state.validation);
export const useProgress = () => useAppStore(state => state.ui.progress || DEFAULT_PROGRESS);
export const useResults = () => useAppStore(state => state.results);
export const useSetResults = () => useAppStore(state => state.setResults);
export const useClearResults = () => useAppStore(state => state.clearResults); 