import React, { useMemo } from 'react';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js';
import type { ChartOptions, Plugin } from 'chart.js';
import { Bar } from 'react-chartjs-2';
import { motion } from 'motion/react';
import { BarChart4, Info } from 'lucide-react';
import { Card } from '../ui/Card';

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
);

interface EffectSizeData {
  effectSizes: number[];
  sampleSize: number;
  label: string;
}

interface EffectSizeDistributionProps {
  datasets: EffectSizeData[];
  title?: string;
  trueEffectSize?: number;
  binCount?: number;
}

export const EffectSizeDistribution: React.FC<EffectSizeDistributionProps> = ({
  datasets,
  title = "Effect Size Distribution",
  trueEffectSize,
  binCount = 30
}) => {
  const chartData = useMemo(() => {
    // Find the range for all datasets
    const allEffectSizes = datasets.flatMap(d => d.effectSizes);
    const minEffect = Math.min(...allEffectSizes);
    const maxEffect = Math.max(...allEffectSizes);
    const binWidth = (maxEffect - minEffect) / binCount;

    // Create bin labels
    const labels = Array(binCount).fill(0).map((_, i) => {
      const binStart = minEffect + i * binWidth;
      const binEnd = minEffect + (i + 1) * binWidth;
      return `${binStart.toFixed(2)}-${binEnd.toFixed(2)}`;
    });

    // Create datasets for Chart.js with white pins at different opacities
    const chartDatasets = datasets.map((dataset, index) => {
      // Create bins for this dataset
      const bins = Array(binCount).fill(0);
      
      dataset.effectSizes.forEach(effect => {
        const binIndex = Math.min(Math.floor((effect - minEffect) / binWidth), binCount - 1);
        bins[binIndex]++;
      });

      // White pins with decreasing opacity for distinction
      const opacity = 0.9 - (index * 0.2);
      return {
        label: dataset.label,
        data: bins,
        backgroundColor: `rgba(255, 255, 255, ${Math.max(opacity, 0.3)})`,
        borderColor: 'rgba(255, 255, 255, 0.2)',
        borderWidth: 0.5,
        borderRadius: 2,
      };
    });

    return {
      labels,
      datasets: chartDatasets
    };
  }, [datasets, binCount]);

  // Custom plugin for true effect size line matching Shannon style
  const trueEffectLinePlugin: Plugin<'bar'> = {
    id: 'trueEffectLine',
    afterDatasetsDraw: (chart) => {
      if (!trueEffectSize) return;
      
      const { ctx, chartArea, scales } = chart;
      if (!chartArea || !scales.x) return;

      // Calculate position based on data range
      const allEffectSizes = datasets.flatMap(d => d.effectSizes);
      const minEffect = Math.min(...allEffectSizes);
      const maxEffect = Math.max(...allEffectSizes);
      const range = maxEffect - minEffect;
      
      if (trueEffectSize >= minEffect && trueEffectSize <= maxEffect) {
        const position = (trueEffectSize - minEffect) / range;
        const xPosition = chartArea.left + (position * chartArea.width);

        ctx.save();
        ctx.strokeStyle = '#22d3ee';
        ctx.lineWidth = 2;
        ctx.setLineDash([3, 3]);
        
        ctx.beginPath();
        ctx.moveTo(xPosition, chartArea.top);
        ctx.lineTo(xPosition, chartArea.bottom);
        ctx.stroke();

        // Label
        ctx.fillStyle = '#22d3ee';
        ctx.font = '10px sans-serif';
        ctx.textAlign = 'center';
        ctx.fillText(`True: ${trueEffectSize.toFixed(2)}`, xPosition, chartArea.top - 5);

        ctx.restore();
      }
    }
  };

  const options: ChartOptions<'bar'> = {
    responsive: true,
    maintainAspectRatio: false,
    layout: {
      padding: {
        top: 20,
        right: 10,
        bottom: 10,
        left: 10
      }
    },
    plugins: {
      legend: {
        display: false
      },
      title: {
        display: false,
        text: title,
        align: 'start',
        color: '#e4e4e7',
        font: {
          size: 14,
          weight: 'bold' as const,
          family: 'Roboto, sans-serif'
        },
        padding: { bottom: 6 }
      },
      tooltip: {
        backgroundColor: 'rgba(39, 39, 42, 0.95)',
        titleColor: '#e4e4e7',
        bodyColor: '#e4e4e7',
        borderColor: '#71717a',
        borderWidth: 1,
        callbacks: {
          title: (tooltipItems) => {
            const item = tooltipItems[0];
            return `Effect size range: ${item.label}`;
          },
          label: (tooltipItem) => {
            return `${tooltipItem.dataset.label}: ${tooltipItem.raw} estimates`;
          },
          afterBody: () => [
            '',
            'Larger samples = more precise estimates',
            'Clustering around true effect shows accuracy'
          ]
        }
      }
    },
    scales: {
      x: {
        title: {
          display: true,
          text: "Effect Size (Cohen's d)",
          color: '#a1a1aa',
          font: { size: 12 }
        },
        ticks: {
          color: '#a1a1aa',
          font: { size: 10 }
        },
        grid: {
          color: 'rgba(113, 113, 122, 0.3)'
        }
      },
      y: {
        title: {
          display: true,
          text: 'Frequency',
          color: '#a1a1aa',
          font: { size: 12 }
        },
        ticks: {
          color: '#a1a1aa',
          font: { size: 10 }
        },
        grid: {
          color: 'rgba(113, 113, 122, 0.3)'
        }
      }
    },
    elements: {
      bar: {
        borderSkipped: false
      }
    },
    animation: {
      duration: 750,
      easing: 'easeOutQuart' as const
    }
  };

  // Calculate summary statistics
  const summaryStats = useMemo(() => {
    return datasets.map(dataset => {
      const effects = dataset.effectSizes;
      const mean = effects.reduce((sum, val) => sum + val, 0) / effects.length;
      const variance = effects.reduce((sum, val) => sum + Math.pow(val - mean, 2), 0) / effects.length;
      const std = Math.sqrt(variance);
      
      return {
        label: dataset.label,
        mean: mean.toFixed(3),
        std: std.toFixed(3),
        min: Math.min(...effects).toFixed(3),
        max: Math.max(...effects).toFixed(3),
        opacity: 0.9 - (datasets.indexOf(dataset) * 0.2)
      };
    });
  }, [datasets]);

  return (
    <motion.div
      className="space-y-4"
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
    >
      <Card className="p-4">
        <div className="flex items-center gap-2 mb-4">
          <BarChart4 className="h-5 w-5 text-cyan-400" />
          <h3 className="text-lg font-semibold text-zinc-200">{title}</h3>
          <div className="ml-auto flex items-center gap-1 text-xs text-zinc-500">
            <Info className="h-3 w-3" />
            Distribution of effect sizes
          </div>
        </div>
        
        <div className="h-80 mb-4">
          <Bar 
            data={chartData} 
            options={options} 
            plugins={[trueEffectLinePlugin]}
          />
        </div>
        
        {/* Legend with opacities matching chart */}
        <div className="flex justify-center mb-4 space-x-4">
          {datasets.map((dataset, index) => (
            <div key={dataset.label} className="flex items-center gap-2">
              <div 
                className="w-3 h-3 rounded"
                style={{ backgroundColor: `rgba(255, 255, 255, ${0.9 - index * 0.2})` }}
              />
              <span className="text-sm text-zinc-300">{dataset.label}</span>
            </div>
          ))}
        </div>
        
        {/* Summary statistics */}
        <div className="space-y-2">
          {summaryStats.map((stats, index) => (
            <div key={index} className="flex justify-between text-xs text-zinc-400">
              <span className="font-medium">{stats.label}:</span>
              <span>
                Mean: {stats.mean}, SD: {stats.std}, Range: [{stats.min}, {stats.max}]
              </span>
            </div>
          ))}
          
          {trueEffectSize && (
            <div className="text-center mt-3 text-sm text-cyan-400 border-t border-zinc-700 pt-2">
              True Effect Size: {trueEffectSize.toFixed(2)}
            </div>
          )}
        </div>
      </Card>
    </motion.div>
  );
}; 