import React, { useRef, useEffect } from 'react';
import { Chart, registerables } from 'chart.js';
import type { ChartConfiguration } from 'chart.js';
import { motion } from 'motion/react';
import { Activity, Info } from 'lucide-react';
import { Card } from '../ui/Card';

Chart.register(...registerables);

interface ShannonSValuesChartProps {
  sValues: number[];
  sampleSize?: number;
  className?: string;
}

export const ShannonSValuesChart: React.FC<ShannonSValuesChartProps> = ({
  sValues,
  sampleSize,
  className = ''
}) => {
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const chartRef = useRef<Chart | null>(null);

  useEffect(() => {
    if (!canvasRef.current || sValues.length === 0) return;

    // Destroy existing chart
    if (chartRef.current) {
      chartRef.current.destroy();
    }

    const ctx = canvasRef.current.getContext('2d');
    if (!ctx) return;

    // Calculate histogram bins for S-values
    const maxSValue = Math.max(...sValues);
    const minSValue = Math.min(...sValues);
    const binCount = Math.min(30, Math.max(10, Math.ceil(Math.sqrt(sValues.length))));
    const binWidth = (maxSValue - minSValue) / binCount;
    
    const bins = Array.from({ length: binCount }, (_, i) => ({
      x: minSValue + i * binWidth,
      count: 0
    }));

    // Fill bins
    sValues.forEach(sValue => {
      const binIndex = Math.min(binCount - 1, Math.floor((sValue - minSValue) / binWidth));
      bins[binIndex].count++;
    });

    // Statistical thresholds for S-values
    const significanceThresholds = [
      { value: -Math.log2(0.05), label: 'α = 0.05', color: '#ef4444' },
      { value: -Math.log2(0.01), label: 'α = 0.01', color: '#dc2626' },
      { value: -Math.log2(0.001), label: 'α = 0.001', color: '#b91c1c' }
    ];

    const config: ChartConfiguration = {
      type: 'bar',
      data: {
        labels: bins.map(bin => bin.x.toFixed(1)),
        datasets: [
          {
            label: 'S-Value Distribution',
            data: bins.map(bin => bin.count),
            backgroundColor: 'rgba(251, 146, 60, 0.7)',
            borderColor: 'rgb(251, 146, 60)',
            borderWidth: 1,
            borderRadius: 4,
          }
        ]
      },
      options: {
        responsive: true,
        maintainAspectRatio: false,
        plugins: {
          title: {
            display: true,
            text: `Shannon S-Values Distribution${sampleSize ? ` (n=${sampleSize})` : ''}`,
            color: '#e4e4e7',
            font: { size: 14, weight: 'bold' }
          },
          legend: {
            display: false
          },
          tooltip: {
            backgroundColor: 'rgba(39, 39, 42, 0.95)',
            titleColor: '#e4e4e7',
            bodyColor: '#e4e4e7',
            borderColor: '#71717a',
            borderWidth: 1,
            callbacks: {
              title: (context) => `S-Value: ${context[0].label}`,
              label: (context) => {
                const count = context.parsed.y;
                const percentage = ((count / sValues.length) * 100).toFixed(1);
                return `Count: ${count} (${percentage}%)`;
              },
              afterBody: () => [
                '',
                'S-Value = -log₂(p-value)',
                'Higher values = stronger evidence against H₀'
              ]
            }
          }
        },
        scales: {
          x: {
            title: {
              display: true,
              text: 'S-Value (bits of information)',
              color: '#a1a1aa',
              font: { size: 12 }
            },
            ticks: {
              color: '#a1a1aa',
              font: { size: 10 }
            },
            grid: {
              color: 'rgba(113, 113, 122, 0.3)'
            }
          },
          y: {
            title: {
              display: true,
              text: 'Frequency',
              color: '#a1a1aa',
              font: { size: 12 }
            },
            ticks: {
              color: '#a1a1aa',
              font: { size: 10 }
            },
            grid: {
              color: 'rgba(113, 113, 122, 0.3)'
            }
          }
        },
        animation: {
          duration: 750,
          easing: 'easeOutQuart'
        }
      },
      plugins: [
        {
          id: 'significanceLines',
          afterDatasetsDraw: (chart) => {
            const { ctx, scales: { x, y } } = chart;
            
            significanceThresholds.forEach(threshold => {
              if (threshold.value >= minSValue && threshold.value <= maxSValue) {
                const xPos = x.getPixelForValue(threshold.value);
                
                ctx.save();
                ctx.strokeStyle = threshold.color;
                ctx.lineWidth = 2;
                ctx.setLineDash([5, 5]);
                
                ctx.beginPath();
                ctx.moveTo(xPos, y.top);
                ctx.lineTo(xPos, y.bottom);
                ctx.stroke();
                
                // Label
                ctx.fillStyle = threshold.color;
                ctx.font = '10px sans-serif';
                ctx.textAlign = 'center';
                ctx.fillText(threshold.label, xPos, y.top - 5);
                
                ctx.restore();
              }
            });
          }
        }
      ]
    };

    chartRef.current = new Chart(ctx, config);

    return () => {
      if (chartRef.current) {
        chartRef.current.destroy();
      }
    };
  }, [sValues, sampleSize]);

  // Calculate statistics
  const meanSValue = sValues.reduce((sum, val) => sum + val, 0) / sValues.length;
  const strongEvidence = sValues.filter(s => s > -Math.log2(0.01)).length;
  const moderateEvidence = sValues.filter(s => s > -Math.log2(0.05) && s <= -Math.log2(0.01)).length;

  return (
    <motion.div
      className={`space-y-4 ${className}`}
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
    >
      <Card className="p-4">
        <div className="flex items-center gap-2 mb-4">
          <Activity className="h-5 w-5 text-orange-400" />
          <h3 className="text-lg font-semibold text-zinc-200">Shannon S-Values</h3>
          <div className="ml-auto flex items-center gap-1 text-xs text-zinc-500">
            <Info className="h-3 w-3" />
            Information against H₀
          </div>
        </div>

        <div className="h-80 mb-4">
          <canvas ref={canvasRef} />
        </div>

        {/* Statistics Summary */}
        <div className="grid grid-cols-3 gap-4 pt-4 border-t border-zinc-700">
          <div className="text-center">
            <div className="text-lg font-semibold text-orange-400">
              {meanSValue.toFixed(2)}
            </div>
            <div className="text-xs text-zinc-400">Mean S-Value</div>
          </div>
          <div className="text-center">
            <div className="text-lg font-semibold text-red-400">
              {strongEvidence}
            </div>
            <div className="text-xs text-zinc-400">Strong Evidence (p &lt; 0.01)</div>
          </div>
          <div className="text-center">
            <div className="text-lg font-semibold text-yellow-400">
              {moderateEvidence}
            </div>
            <div className="text-xs text-zinc-400">Moderate Evidence (p &lt; 0.05)</div>
          </div>
        </div>

        {/* Educational Note */}
        <div className="mt-4 p-3 bg-orange-900/20 border border-orange-800/30 rounded-md">
          <div className="text-xs text-orange-200 leading-relaxed">
            <strong>S-Values (Shannon Information):</strong> Measure the information content against the null hypothesis. 
            Higher S-values indicate stronger evidence. S = -log₂(p), so p = 0.05 → S = 4.32 bits.
          </div>
        </div>
      </Card>
    </motion.div>
  );
}; 