import React, { useRef, useEffect } from 'react';
import { Chart, registerables } from 'chart.js';
import type { ChartConfiguration } from 'chart.js';
import { motion } from 'motion/react';
import { Target, TrendingDown } from 'lucide-react';
import { Card } from '../ui/Card';

Chart.register(...registerables);

interface CIGapChartProps {
  ciGaps: number[];
  sampleSize?: number;
  confidenceLevel?: number;
  className?: string;
}

export const CIGapChart: React.FC<CIGapChartProps> = ({
  ciGaps,
  sampleSize,
  confidenceLevel = 0.95,
  className = ''
}) => {
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const chartRef = useRef<Chart | null>(null);

  useEffect(() => {
    if (!canvasRef.current || ciGaps.length === 0) return;

    // Destroy existing chart
    if (chartRef.current) {
      chartRef.current.destroy();
    }

    const ctx = canvasRef.current.getContext('2d');
    if (!ctx) return;

    // Calculate histogram bins for CI gaps
    const maxGap = Math.max(...ciGaps);
    const minGap = Math.min(...ciGaps);
    const binCount = Math.min(25, Math.max(10, Math.ceil(Math.sqrt(ciGaps.length))));
    const binWidth = (maxGap - minGap) / binCount;
    
    const bins = Array.from({ length: binCount }, (_, i) => ({
      x: minGap + i * binWidth + binWidth / 2, // Center of bin
      count: 0,
      range: `${(minGap + i * binWidth).toFixed(2)} - ${(minGap + (i + 1) * binWidth).toFixed(2)}`
    }));

    // Fill bins
    ciGaps.forEach(gap => {
      const binIndex = Math.min(binCount - 1, Math.floor((gap - minGap) / binWidth));
      bins[binIndex].count++;
    });

    // Calculate mean for reference line
    const meanGap = ciGaps.reduce((sum, gap) => sum + gap, 0) / ciGaps.length;

    const config: ChartConfiguration = {
      type: 'bar',
      data: {
        labels: bins.map(bin => bin.x.toFixed(2)),
        datasets: [
          {
            label: 'CI Gap Distribution',
            data: bins.map(bin => bin.count),
            backgroundColor: 'rgba(34, 197, 94, 0.7)',
            borderColor: 'rgb(34, 197, 94)',
            borderWidth: 1,
            borderRadius: 4,
          }
        ]
      },
      options: {
        responsive: true,
        maintainAspectRatio: false,
        plugins: {
          title: {
            display: true,
            text: `${(confidenceLevel * 100).toFixed(0)}% Confidence Interval Gaps${sampleSize ? ` (n=${sampleSize})` : ''}`,
            color: '#e4e4e7',
            font: { size: 14, weight: 'bold' }
          },
          legend: {
            display: false
          },
          tooltip: {
            backgroundColor: 'rgba(39, 39, 42, 0.95)',
            titleColor: '#e4e4e7',
            bodyColor: '#e4e4e7',
            borderColor: '#71717a',
            borderWidth: 1,
            callbacks: {
              title: (context) => `CI Gap: ${context[0].label}`,
              label: (context) => {
                const count = context.parsed.y;
                const percentage = ((count / ciGaps.length) * 100).toFixed(1);
                const binIndex = context.dataIndex;
                return [
                  `Count: ${count} (${percentage}%)`,
                  `Range: ${bins[binIndex].range}`
                ];
              },
              afterBody: () => [
                '',
                'Smaller gaps = more precise estimates',
                'Precision improves with larger sample sizes'
              ]
            }
          }
        },
        scales: {
          x: {
            title: {
              display: true,
              text: 'Confidence Interval Gap Width',
              color: '#a1a1aa',
              font: { size: 12 }
            },
            ticks: {
              color: '#a1a1aa',
              font: { size: 10 }
            },
            grid: {
              color: 'rgba(113, 113, 122, 0.3)'
            }
          },
          y: {
            title: {
              display: true,
              text: 'Frequency',
              color: '#a1a1aa',
              font: { size: 12 }
            },
            ticks: {
              color: '#a1a1aa',
              font: { size: 10 }
            },
            grid: {
              color: 'rgba(113, 113, 122, 0.3)'
            }
          }
        },
        animation: {
          duration: 750,
          easing: 'easeOutQuart'
        }
      },
      plugins: [
        {
          id: 'meanLine',
          afterDatasetsDraw: (chart) => {
            const { ctx, scales: { x, y } } = chart;
            
            if (meanGap >= minGap && meanGap <= maxGap) {
              const xPos = x.getPixelForValue(meanGap);
              
              ctx.save();
              ctx.strokeStyle = '#22d3ee';
              ctx.lineWidth = 2;
              ctx.setLineDash([3, 3]);
              
              ctx.beginPath();
              ctx.moveTo(xPos, y.top);
              ctx.lineTo(xPos, y.bottom);
              ctx.stroke();
              
              // Label
              ctx.fillStyle = '#22d3ee';
              ctx.font = 'bold 10px sans-serif';
              ctx.textAlign = 'center';
              ctx.fillText(`Mean: ${meanGap.toFixed(2)}`, xPos, y.top - 5);
              
              ctx.restore();
            }
          }
        }
      ]
    };

    chartRef.current = new Chart(ctx, config);

    return () => {
      if (chartRef.current) {
        chartRef.current.destroy();
      }
    };
  }, [ciGaps, sampleSize, confidenceLevel]);

  // Calculate precision statistics
  const meanGap = ciGaps.reduce((sum, gap) => sum + gap, 0) / ciGaps.length;
  const stdGap = Math.sqrt(ciGaps.reduce((sum, gap) => sum + Math.pow(gap - meanGap, 2), 0) / ciGaps.length);
  const minGap = Math.min(...ciGaps);
  const maxGap = Math.max(...ciGaps);
  const medianGap = [...ciGaps].sort((a, b) => a - b)[Math.floor(ciGaps.length / 2)];

  // Precision categories
  const narrowGaps = ciGaps.filter(gap => gap < meanGap - stdGap / 2).length;
  const wideGaps = ciGaps.filter(gap => gap > meanGap + stdGap / 2).length;

  return (
    <motion.div
      className={`space-y-4 ${className}`}
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
    >
      <Card className="p-4">
        <div className="flex items-center gap-2 mb-4">
          <Target className="h-5 w-5 text-teal-400" />
          <h3 className="text-lg font-semibold text-zinc-200">CI Gap Analysis</h3>
          <div className="ml-auto flex items-center gap-1 text-xs text-zinc-500">
            <TrendingDown className="h-3 w-3" />
            Precision Visualization
          </div>
        </div>

        <div className="h-80 mb-4">
          <canvas ref={canvasRef} />
        </div>

        {/* Statistics Summary */}
        <div className="grid grid-cols-2 gap-4 mb-4">
          <div className="space-y-2">
            <div className="flex justify-between text-sm">
              <span className="text-zinc-400">Mean Gap:</span>
              <span className="text-teal-400 font-medium">{meanGap.toFixed(3)}</span>
            </div>
            <div className="flex justify-between text-sm">
              <span className="text-zinc-400">Std Deviation:</span>
              <span className="text-zinc-300">{stdGap.toFixed(3)}</span>
            </div>
            <div className="flex justify-between text-sm">
              <span className="text-zinc-400">Median Gap:</span>
              <span className="text-zinc-300">{medianGap.toFixed(3)}</span>
            </div>
          </div>
          <div className="space-y-2">
            <div className="flex justify-between text-sm">
              <span className="text-zinc-400">Min Gap:</span>
              <span className="text-green-400 font-medium">{minGap.toFixed(3)}</span>
            </div>
            <div className="flex justify-between text-sm">
              <span className="text-zinc-400">Max Gap:</span>
              <span className="text-red-400 font-medium">{maxGap.toFixed(3)}</span>
            </div>
            <div className="flex justify-between text-sm">
              <span className="text-zinc-400">Range:</span>
              <span className="text-zinc-300">{(maxGap - minGap).toFixed(3)}</span>
            </div>
          </div>
        </div>

        {/* Precision Categories */}
        <div className="grid grid-cols-3 gap-4 pt-4 border-t border-zinc-700">
          <div className="text-center">
            <div className="text-lg font-semibold text-green-400">
              {narrowGaps}
            </div>
            <div className="text-xs text-zinc-400">High Precision</div>
            <div className="text-xs text-zinc-500">(narrow gaps)</div>
          </div>
          <div className="text-center">
            <div className="text-lg font-semibold text-yellow-400">
              {ciGaps.length - narrowGaps - wideGaps}
            </div>
            <div className="text-xs text-zinc-400">Moderate Precision</div>
            <div className="text-xs text-zinc-500">(typical gaps)</div>
          </div>
          <div className="text-center">
            <div className="text-lg font-semibold text-red-400">
              {wideGaps}
            </div>
            <div className="text-xs text-zinc-400">Low Precision</div>
            <div className="text-xs text-zinc-500">(wide gaps)</div>
          </div>
        </div>

        {/* Educational Note */}
        <div className="mt-4 p-3 bg-teal-900/20 border border-teal-800/30 rounded-md">
          <div className="text-xs text-teal-200 leading-relaxed">
            <strong>Confidence Interval Gaps:</strong> Show estimation precision. Narrower gaps indicate more precise estimates. 
            Precision improves with larger sample sizes and decreases with higher confidence levels.
          </div>
        </div>
      </Card>
    </motion.div>
  );
}; 