import React, { useState, useEffect } from 'react';
import { useAppStore, useCurrentConfig } from '../../store/index';
import { useUIStore } from '../../stores/uiStore';
import { useDesktop } from '../../hooks/useDesktop';
import { ThemeToggle } from '../ui/ThemeToggle';
import { Button } from '../ui/Button';

import { EnhancedAnalysisCard } from './EnhancedAnalysisCard';
import { Plus, BookOpen, FolderOpen, BarChart3 } from 'lucide-react';
import type { FlexibleSimulationConfig, ConfigurationTemplate } from '../../types/index';

// Simple date formatting utility - commented out (unused)
// const formatTimeAgo = (date: Date): string => {
