export const LeftSidebar: React.FC = () => {
  const toggleLeftSidebar = useUIStore(state => state.toggleLeftSidebar);
  const { 
    setCurrentConfig, 
    loadTemplate, 
    saveAsTemplate, 
    templates 
  } = useAppStore();
  const currentConfig = useCurrentConfig();
  const { isDesktop, fileOperations, notifications } = useDesktop();
  const [showTemplates, setShowTemplates] = useState(false);
  const [showSaveDialog, setShowSaveDialog] = useState(false);
  const [showQuickActionsDropdown, setShowQuickActionsDropdown] = useState(false);
  const [templateName, setTemplateName] = useState('');
  const [templateDescription, setTemplateDescription] = useState('');
  const [isLoading, setIsLoading] = useState(false);

  // Debug effect to track showSaveDialog state changes
  useEffect(() => {
    console.log('showSaveDialog state changed:', showSaveDialog);
  }, [showSaveDialog]);

  // Close dropdown when clicking outside
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (showQuickActionsDropdown && !(event.target as Element).closest('.dropdown-container')) {
        setShowQuickActionsDropdown(false);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [showQuickActionsDropdown]);

  // Built-in templates for simulation scenarios
  const builtInTemplates: ConfigurationTemplate[] = [
    {
      id: 'notebook-reproduction',
      name: 'Legacy Notebook Reproduction',
      description: 'Reproduces the original Jupyter notebook simulation with multiple sample sizes',
      isBuiltIn: true,
      config: {
        id: 'template-notebook',
        name: 'Legacy Notebook Reproduction',
        samplePairs: [
          {
            id: 'pair-n10',
            name: 'Small Sample (n=10)',
            population1: { mean: 0, std: 1, size: 1000 },
            population2: { mean: 0.5, std: 1, size: 1000 },
            sampleSize: 10
          },
          {
            id: 'pair-n30',
            name: 'Medium Sample (n=30)',
            population1: { mean: 0, std: 1, size: 1000 },
            population2: { mean: 0.5, std: 1, size: 1000 },
            sampleSize: 30
          },
          {
            id: 'pair-n60',
            name: 'Large Sample (n=60)',
            population1: { mean: 0, std: 1, size: 1000 },
            population2: { mean: 0.5, std: 1, size: 1000 },
            sampleSize: 60
          },
          {
            id: 'pair-n100',
            name: 'Very Large Sample (n=100)',
            population1: { mean: 0, std: 1, size: 1000 },
            population2: { mean: 0.5, std: 1, size: 1000 },
            sampleSize: 100
          }
        ],
        simulationCount: 1000,
        significanceThresholds: [0.5, 0.1, 0.05, 0.01, 0.001],
        confidenceLevel: 0.95,
        createdAt: new Date(),
        updatedAt: new Date()
      }
    },
    {
      id: 'null-hypothesis',
      name: 'Null Hypothesis Test',
      description: 'Test scenarios where populations have identical means (null hypothesis is true)',
      isBuiltIn: true,
      config: {
        id: 'template-null',
        name: 'Null Hypothesis Test',
        samplePairs: [
          {
            id: 'pair-null-small',
            name: 'Null Test (n=20)',
            population1: { mean: 100, std: 15, size: 1000 },
            population2: { mean: 100, std: 15, size: 1000 },
            sampleSize: 20
          },
          {
            id: 'pair-null-large',
            name: 'Null Test (n=50)',
            population1: { mean: 100, std: 15, size: 1000 },
            population2: { mean: 100, std: 15, size: 1000 },
            sampleSize: 50
          }
        ],
        simulationCount: 1000,
        significanceThresholds: [0.5, 0.1, 0.05, 0.01, 0.001],
        confidenceLevel: 0.95,
        createdAt: new Date(),
        updatedAt: new Date()
      }
    },
    {
      id: 'effect-size-comparison',
      name: 'Effect Size Comparison',
      description: 'Compare statistical power across different effect sizes',
      isBuiltIn: true,
      config: {
        id: 'template-effect',
        name: 'Effect Size Comparison',
        samplePairs: [
          {
            id: 'pair-small-effect',
            name: 'Small Effect (d=0.2)',
            population1: { mean: 100, std: 15, size: 1000 },
            population2: { mean: 103, std: 15, size: 1000 },
            sampleSize: 30
          },
          {
            id: 'pair-medium-effect',
            name: 'Medium Effect (d=0.5)',
            population1: { mean: 100, std: 15, size: 1000 },
            population2: { mean: 107.5, std: 15, size: 1000 },
            sampleSize: 30
          },
          {
            id: 'pair-large-effect',
            name: 'Large Effect (d=0.8)',
            population1: { mean: 100, std: 15, size: 1000 },
            population2: { mean: 112, std: 15, size: 1000 },
            sampleSize: 30
          }
        ],
        simulationCount: 1000,
        significanceThresholds: [0.5, 0.1, 0.05, 0.01, 0.001],
        confidenceLevel: 0.95,
        createdAt: new Date(),
        updatedAt: new Date()
      }
    }
  ];

  const handleCreateNew = () => {
    const newConfig: FlexibleSimulationConfig = {
      id: `sim-${Date.now()}`,
      name: `New Simulation ${new Date().toLocaleTimeString()}`,
      samplePairs: [
        {
          id: `pair-${Date.now()}`,
          name: 'Sample Pair 1',
          population1: { mean: 100, std: 15, size: 1000 },
          population2: { mean: 110, std: 15, size: 1000 },
          sampleSize: 30
        }
      ],
      simulationCount: 1000,
      significanceThresholds: [0.5, 0.1, 0.05, 0.01, 0.001],
      confidenceLevel: 0.95,
      createdAt: new Date(),
      updatedAt: new Date()
    };
    setCurrentConfig(newConfig);
  };

  const handleLoadTemplate = (template: ConfigurationTemplate) => {
    loadTemplate(template);
    setShowTemplates(false);
  };

  const handleSaveAsTemplate = () => {
    if (currentConfig && templateName.trim()) {
      saveAsTemplate(templateName.trim(), templateDescription.trim());
      setShowSaveDialog(false);
      setTemplateName('');
      setTemplateDescription('');
    }
  };

  const handleUpdateConfig = (updatedConfig: FlexibleSimulationConfig) => {
    setCurrentConfig(updatedConfig);
  };

  const handleSaveConfig = async () => {
    if (!currentConfig || !isDesktop) return;
    
    setIsLoading(true);
    try {
      const defaultFilename = `${currentConfig.name.replace(/[^a-zA-Z0-9]/g, '_')}.json`;
      const filePath = await fileOperations.showSaveDialog(defaultFilename);
      
      if (filePath) {
        await fileOperations.saveConfig(currentConfig, filePath);
        await notifications.send('Configuration Saved', `Saved to ${filePath}`);
      }
    } catch (error) {
      console.error('Failed to save configuration:', error);
      await notifications.send('Save Failed', 'Could not save configuration file');
    } finally {
      setIsLoading(false);
    }
  };

  const handleLoadConfig = async () => {
    if (!isDesktop) return;
    
    setIsLoading(true);
    try {
      const filePath = await fileOperations.showOpenDialog();
      
      if (filePath) {
        const loadedConfig = await fileOperations.loadConfig(filePath);
        setCurrentConfig(loadedConfig);
        await notifications.send('Configuration Loaded', `Loaded from ${filePath}`);
      }
    } catch (error) {
      console.error('Failed to load configuration:', error);
      await notifications.send('Load Failed', 'Could not load configuration file');
    } finally {
      setIsLoading(false);
    }
  };

  const handleExportResults = async () => {
    if (!currentConfig?.results || !isDesktop) return;
    
    setIsLoading(true);
    try {
      const defaultFilename = `${currentConfig.name.replace(/[^a-zA-Z0-9]/g, '_')}_results.json`;
      const filePath = await fileOperations.showSaveDialog(defaultFilename);
      
      if (filePath) {
        await fileOperations.exportResults(currentConfig, currentConfig.results, 'json', filePath);
        await notifications.sendExportComplete(filePath);
      }
    } catch (error) {
      console.error('Failed to export results:', error);
      await notifications.send('Export Failed', 'Could not export results');
    } finally {
      setIsLoading(false);
    }
  };

  const allTemplates = [...builtInTemplates, ...templates];

  return (
    <div className="h-full flex flex-col bg-sidebar border-r border-border">
      {/* Header */}
      <div className="flex items-center justify-between p-3 border-b border-border">
        <h2 className="text-sidebar-foreground text-sm">Simulations</h2>
        <div className="flex items-center gap-1">
          
          {/* Quick Actions */}
          <div className="relative dropdown-container">
            <button
              onClick={() => setShowQuickActionsDropdown(!showQuickActionsDropdown)}
              className="h-7 w-7 p-0 flex items-center justify-center rounded hover:bg-sidebar-accent transition-colors text-sidebar-foreground"
              title="Quick Actions"
            >
              <Plus className="h-3 w-3" />
            </button>
            
            {/* Quick Actions Dropdown */}
            {showQuickActionsDropdown && (
              <div className="absolute top-8 right-0 w-48 bg-sidebar border border-border rounded shadow-lg z-[9999]">
                <div className="p-1 space-y-0.5">
                  <button
                    onClick={() => {
                      handleCreateNew();
                      setShowQuickActionsDropdown(false);
                    }}
                    className="w-full flex items-center gap-2 px-2 py-1.5 text-xs text-sidebar-foreground hover:bg-sidebar-accent rounded transition-colors"
                  >
                    <Plus className="h-3 w-3" />
                    Create New Simulation
                  </button>
                  
                  <button
                    onClick={() => {
                      setShowTemplates(!showTemplates);
                      setShowQuickActionsDropdown(false);
                    }}
                    className="w-full flex items-center gap-2 px-2 py-1.5 text-xs text-sidebar-foreground hover:bg-sidebar-accent rounded transition-colors"
                  >
                    <BookOpen className="h-3 w-3" />
                    Load Template
                  </button>

                  {isDesktop && (
                    <button
                      onClick={() => {
                        handleLoadConfig();
                        setShowQuickActionsDropdown(false);
                      }}
                      disabled={isLoading}
                      className="w-full flex items-center gap-2 px-2 py-1.5 text-xs text-sidebar-foreground hover:bg-sidebar-accent rounded transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
                    >
                      <FolderOpen className="h-3 w-3" />
                      Load Config File
                    </button>
                  )}
                </div>
              </div>
            )}
          </div>
          
          <button 
            onClick={toggleLeftSidebar}
            className="h-7 w-7 p-0 flex items-center justify-center rounded hover:bg-sidebar-accent transition-colors text-sidebar-foreground"
          >
            <span className="text-xs">←</span>
          </button>
          
          <ThemeToggle />
        </div>
      </div>

      {/* Analysis List */}
      <div className="flex-1 overflow-auto">
        {/* Templates Section */}
        {showTemplates && (
          <div className="border-b border-border">
            <div className="p-2">
              <h4 className="text-xs text-muted-foreground border-b border-border pb-1">
                Built-in Templates
              </h4>
              <div className="mt-1 space-y-0.5">
                {allTemplates.map((template) => (
                  <button
                    key={template.id}
                    onClick={() => handleLoadTemplate(template)}
                    className="w-full text-left p-2 rounded hover:bg-sidebar-accent transition-colors"
                  >
                    <h5 className="text-xs text-sidebar-foreground truncate">
                      {template.name}
                    </h5>
                    <p className="text-[10px] text-muted-foreground mt-0.5 truncate">
                      {template.description}
                    </p>
                  </button>
                ))}
              </div>
            </div>
          </div>
        )}

        {/* Analysis Items */}
        {currentConfig ? (
          <div className="p-2 space-y-1">
            <EnhancedAnalysisCard
              currentConfig={currentConfig}
              onSaveTemplate={() => {
                console.log('onSaveTemplate called, setting showSaveDialog to true');
                setShowSaveDialog(true);
              }}
              onSaveConfig={handleSaveConfig}
              onExportResults={handleExportResults}
              onUpdateConfig={handleUpdateConfig}
              isDesktop={isDesktop}
              isLoading={isLoading}
            />
          </div>
        ) : (
          <div className="p-4 text-center text-muted-foreground">
            <BarChart3 className="h-8 w-8 mx-auto mb-2 opacity-50" />
            <p className="text-xs">No active simulation</p>
            <p className="text-[10px] mt-1">Create new or load template</p>
          </div>
        )}
      </div>

      {/* Save Template Section */}
      {showSaveDialog && (
        <div className="border-t border-border p-2">
          <h3 className="text-sidebar-foreground text-xs mb-2">Save as Template</h3>
          <div className="space-y-2">
            <input
              type="text"
              value={templateName}
              onChange={(e) => setTemplateName(e.target.value)}
              placeholder="Template name"
              className="w-full px-2 py-1 text-xs bg-sidebar-accent border border-border rounded text-sidebar-foreground"
            />
            <textarea
              value={templateDescription}
              onChange={(e) => setTemplateDescription(e.target.value)}
              placeholder="Description (optional)"
              rows={2}
              className="w-full px-2 py-1 text-xs bg-sidebar-accent border border-border rounded text-sidebar-foreground"
            />
            <div className="flex gap-1">
              <Button
                size="sm"
                onClick={handleSaveAsTemplate}
                disabled={!templateName.trim()}
                className="flex-1"
              >
                Save
              </Button>
              <Button
                size="sm"
                variant="outline"
                onClick={() => {
                  console.log('Cancel clicked, setting showSaveDialog to false');
                  setShowSaveDialog(false);
                }}
                className="flex-1"
              >
                Cancel
              </Button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}; 