import React, { useMemo } from 'react';
import { motion, AnimatePresence } from 'motion/react';
import { 
  BarChart3, TrendingUp, Target, Download,
  Eye, EyeOff, FileImage,
  Settings2, Activity, FileText, Lightbulb
} from 'lucide-react';
import { Button } from '../ui/Button';
import { Card } from '../ui/Card';
import { 
  // Individual primitive selectors
  usePValueHistogramVisible,
  useEffectSizeDistributionVisible,
  useStatisticalPowerTableVisible,
  useShannonSValuesVisible,
  useCiGapChartVisible,
  usePValueHistogramMode,
  useEffectSizeDistributionMode,
  // Export settings as individual primitives
  useExportQuality,
  // Individual function selectors
  useToggleChartVisibility,
  useShowAllCharts,
  useHideAllCharts,
  useSetExportQuality,
  useResetToDefaults,
  // Other selectors
  useCurrentResults, 
  useHasMultiplePairs, 
  useCurrentInsights
} from '../../stores/chartsStore';

interface ChartsPanelProps {
  className?: string;
}

export const ChartsPanel: React.FC<ChartsPanelProps> = ({
  className = ''
}) => {
  // Use individual primitive selectors instead of object selectors
  const pValueHistogramVisible = usePValueHistogramVisible();
  const effectSizeDistributionVisible = useEffectSizeDistributionVisible();
  const statisticalPowerTableVisible = useStatisticalPowerTableVisible();
  const shannonSValuesVisible = useShannonSValuesVisible();
  const ciGapChartVisible = useCiGapChartVisible();
  
  const pValueHistogramMode = usePValueHistogramMode();
  const effectSizeDistributionMode = useEffectSizeDistributionMode();
  
  // Export settings as individual primitives
  // const exportFormat = useExportFormat(); // Unused
  const exportQuality = useExportQuality();
  // const includeInsights = useIncludeInsights(); // Unused
  
  // Individual function selectors
  const toggleChartVisibility = useToggleChartVisibility();
  // const setChartMode = useSetChartMode(); // Unused
  const showAllCharts = useShowAllCharts();
  const hideAllCharts = useHideAllCharts();
  // const setExportFormat = useSetExportFormat(); // Unused
  const setExportQuality = useSetExportQuality();
  const resetToDefaults = useResetToDefaults();
  
  const currentResults = useCurrentResults();
  const hasMultiplePairs = useHasMultiplePairs();
  const currentInsights = useCurrentInsights();

  // Chart configuration for the UI - now using primitive values
  const chartConfigs = useMemo(() => [
    {
      id: 'pValueHistogram' as const,
      name: 'P-Value Distribution',
      description: 'Shows statistical significance patterns',
      icon: <BarChart3 className="h-4 w-4" />,
      enabled: pValueHistogramVisible,
      hasMode: hasMultiplePairs,
      mode: pValueHistogramMode,
      color: 'blue'
    },
    {
      id: 'effectSizeDistribution' as const,
      name: 'Effect Size Distribution',
      description: 'Demonstrates estimation precision',
      icon: <TrendingUp className="h-4 w-4" />,
      enabled: effectSizeDistributionVisible,
      hasMode: hasMultiplePairs,
      mode: effectSizeDistributionMode,
      color: 'purple'
    },
    {
      id: 'statisticalPowerTable' as const,
      name: 'Statistical Power Table',
      description: 'Power analysis across conditions',
      icon: <Target className="h-4 w-4" />,
      enabled: statisticalPowerTableVisible,
      hasMode: false,
      color: 'green'
    },
    {
      id: 'shannonSValues' as const,
      name: 'Shannon S-Values',
      description: 'Information against null hypothesis',
      icon: <Activity className="h-4 w-4" />,
      enabled: shannonSValuesVisible,
      hasMode: false,
      color: 'orange'
    },
    {
      id: 'ciGapChart' as const,
      name: 'CI Gap Visualization',
      description: 'Confidence interval precision',
      icon: <Target className="h-4 w-4" />,
      enabled: ciGapChartVisible,
      hasMode: false,
      color: 'teal'
    }
  ], [
    pValueHistogramVisible, 
    effectSizeDistributionVisible, 
    statisticalPowerTableVisible, 
    shannonSValuesVisible, 
    ciGapChartVisible,
    pValueHistogramMode, 
    effectSizeDistributionMode, 
    hasMultiplePairs
  ]);

  const enabledChartsCount = chartConfigs.filter(c => c.enabled).length;

  // Map simple color names to Tailwind palette hex values for inline styling
  // const colorHex: Record<string, string> = { // Unused
  //   blue: '#2563eb',      // blue-600
  //   purple: '#7c3aed',    // purple-600
  //   green: '#16a34a',     // green-600
  //   orange: '#ea580c',    // orange-600
  //   teal: '#0d9488'       // teal-600
  // };

  // Export functionality with comprehensive options
  const handleExport = async (format: 'png' | 'svg' | 'pdf') => {
    if (!currentResults) return;
    
    try {
      // Collect all visible chart canvases
      const chartCanvases: any[] = [];
      
      // Find chart canvases in the DOM
      const canvasElements = document.querySelectorAll('canvas');
      canvasElements.forEach((canvas, index) => {
        if (canvas.offsetParent !== null) { // Only visible canvases
          chartCanvases.push({
            chartId: `chart_${index}`,
            title: `Chart_${index}`,
            canvas: canvas
          });
        }
      });

      if (chartCanvases.length === 0) {
        console.warn('No visible charts found for export');
        return;
      }

      // For now, export each chart individually
      // In a full implementation, we'd use the useChartExport hook
      chartCanvases.forEach((chartData, index) => {
        const dataURL = chartData.canvas.toDataURL('image/png', 2);
        const link = document.createElement('a');
        link.download = `statdash_chart_${index + 1}.png`;
        link.href = dataURL;
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
      });

      console.log(`Exported ${chartCanvases.length} charts as ${format}`);
    } catch (error) {
      console.error('Export failed:', error);
    }
  };

  // Export data functionality
  const handleDataExport = async (format: 'json' | 'csv') => {
    if (!currentResults) return;
    
    try {
      const timestamp = new Date().toISOString().slice(0, 19).replace(/:/g, '-');
      
      if (format === 'json') {
        const exportData = {
          metadata: {
            exportDate: new Date().toISOString(),
            simulationCount: currentResults.pValues.length,
            hasMultiplePairs: Boolean(currentResults.samplePairResults && currentResults.samplePairResults.length > 1)
          },
          results: {
            pValues: currentResults.pValues,
            effectSizes: currentResults.effectSize,
            ciGaps: currentResults.ciGaps,
            sValues: currentResults.sValues,
            samplePairResults: currentResults.samplePairResults
          }
        };
        
        const jsonString = JSON.stringify(exportData, null, 2);
        const blob = new Blob([jsonString], { type: 'application/json' });
        const url = URL.createObjectURL(blob);
        const link = document.createElement('a');
        link.download = `statdash_data_${timestamp}.json`;
        link.href = url;
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        URL.revokeObjectURL(url);
      } else if (format === 'csv') {
        const headers = ['simulation_id', 'p_value', 'effect_size', 'ci_gap', 's_value'];
        const rows = currentResults.pValues.map((pValue, index) => [
          index + 1,
          pValue.toFixed(6),
          currentResults.effectSize[index].toFixed(6),
          currentResults.ciGaps[index].toFixed(6),
          currentResults.sValues[index].toFixed(6)
        ]);
        
        const csvContent = [headers.join(','), ...rows.map(row => row.join(','))].join('\n');
        const blob = new Blob([csvContent], { type: 'text/csv' });
        const url = URL.createObjectURL(blob);
        const link = document.createElement('a');
        link.download = `statdash_data_${timestamp}.csv`;
        link.href = url;
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        URL.revokeObjectURL(url);
      }
    } catch (error) {
      console.error('Data export failed:', error);
    }
  };

  return (
    <div className={`space-y-4 ${className}`}>
      {/* Header */}
      <div className="flex items-center justify-between mb-4">
        <div className="flex items-center gap-2">
          <BarChart3 className="h-4 w-4 text-blue-400" />
          <h3 className="font-medium text-zinc-200">Charts Control</h3>
        </div>
        {currentResults && (
          <div className="text-xs text-zinc-500">
            {hasMultiplePairs ? 'Multi-Size Analysis' : 'Single Analysis'}
          </div>
        )}
      </div>

      {/* Quick Actions */}
      <Card className="p-3">
        <div className="flex items-center justify-between mb-3">
          <h4 className="text-sm font-medium text-zinc-300">Quick Actions</h4>
          <div className="text-xs text-zinc-500">{enabledChartsCount}/{chartConfigs.length} visible</div>
        </div>
        <div className="grid grid-cols-2 gap-2">
          <Button
            variant="secondary"
            size="sm"
            onClick={showAllCharts}
            className="text-xs"
          >
            Show All
          </Button>
          <Button
            variant="secondary"
            size="sm"
            onClick={hideAllCharts}
            className="text-xs"
          >
            Hide All
          </Button>
        </div>
      </Card>

      {/* Chart Configuration */}
      <Card className="p-4">
        <h4 className="text-sm font-medium text-zinc-300 mb-3 flex items-center gap-2">
          <Settings2 className="h-4 w-4" />
          Visualization Controls
        </h4>
        <div className="space-y-3">
          {chartConfigs.map(config => (
            <motion.div 
              key={config.id} 
              className="space-y-2"
              layout
              initial={{ opacity: 0, y: 10 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.2 }}
            >
              {/* Chart Toggle */}
              <div className="flex items-start gap-3">
                <Button
                  variant="ghost"
                  size="sm"
                  onClick={() => toggleChartVisibility(config.id)}
                  className={`p-2 ${
                    config.enabled 
                      ? `text-${config.color}-400 bg-${config.color}-400/10` 
                      : 'text-zinc-500 hover:text-zinc-300'
                  }`}
                >
                  {config.enabled ? <Eye className="h-4 w-4" /> : <EyeOff className="h-4 w-4" />}
                </Button>
                
                <div className="flex-1">
                  <div className="flex items-center gap-2 mb-1">
                    {config.icon}
                    <span className={`text-sm font-medium ${
                      config.enabled ? 'text-zinc-200' : 'text-zinc-500'
                    }`}>
                      {config.name}
                    </span>
                  </div>
                  <p className="text-xs text-zinc-400">
                    {config.description}
                  </p>
                </div>
              </div>

              {/* Mode Toggle removed to avoid redundant controls */}
            </motion.div>
          ))}
        </div>
      </Card>

      {/* Dynamic Insights */}
      {currentInsights.length > 0 && (
        <Card className="p-4">
          <h4 className="text-sm font-medium text-zinc-300 mb-3 flex items-center gap-2">
            <Lightbulb className="h-4 w-4 text-yellow-400" />
            Current Insights
          </h4>
          <div className="space-y-2">
            <AnimatePresence>
              {currentInsights.map((insight, index) => (
                <motion.div
                  key={`insight-${index}`}
                  className="p-3 bg-blue-900/20 border border-blue-800/30 rounded-md"
                  initial={{ opacity: 0, y: 10 }}
                  animate={{ opacity: 1, y: 0 }}
                  exit={{ opacity: 0, y: -10 }}
                  transition={{ delay: index * 0.1 }}
                >
                  <p className="text-xs text-blue-200 leading-relaxed">
                    {insight}
                  </p>
                </motion.div>
              ))}
            </AnimatePresence>
          </div>
        </Card>
      )}

      {/* Export Options */}
      {currentResults && (
        <Card className="p-4">
          <h4 className="text-sm font-medium text-zinc-300 mb-3 flex items-center gap-2">
            <Download className="h-4 w-4" />
            Export Options
          </h4>
          <div className="space-y-3">
            {/* Chart Export */}
            <div>
              <div className="text-xs text-zinc-400 mb-2">Charts</div>
              <div className="grid grid-cols-2 gap-2">
                <Button
                  variant="secondary"
                  size="sm"
                  onClick={() => handleExport('png')}
                  className="text-xs flex items-center gap-1"
                >
                  <FileImage className="h-3 w-3" />
                  PNG
                </Button>
                <Button
                  variant="secondary"
                  size="sm"
                  onClick={() => handleExport('svg')}
                  className="text-xs flex items-center gap-1"
                >
                  <FileImage className="h-3 w-3" />
                  SVG
                </Button>
              </div>
            </div>

            {/* Data Export */}
            <div>
              <div className="text-xs text-zinc-400 mb-2">Raw Data</div>
              <div className="grid grid-cols-2 gap-2">
                <Button
                  variant="secondary"
                  size="sm"
                  onClick={() => handleDataExport('json')}
                  className="text-xs flex items-center gap-1"
                >
                  <FileText className="h-3 w-3" />
                  JSON
                </Button>
                <Button
                  variant="secondary"
                  size="sm"
                  onClick={() => handleDataExport('csv')}
                  className="text-xs flex items-center gap-1"
                >
                  <FileText className="h-3 w-3" />
                  CSV
                </Button>
              </div>
            </div>
          
            {/* Export Settings */}
            <div className="text-xs text-zinc-500 pt-2 border-t border-zinc-700">
              <div className="flex justify-between items-center">
                <span>Quality:</span>
                <select 
                  value={exportQuality}
                  onChange={(e) => setExportQuality(e.target.value as 'standard' | 'high')}
                  className="bg-zinc-800 border border-zinc-600 rounded px-2 py-1 text-xs"
                >
                  <option value="standard">Standard</option>
                  <option value="high">High</option>
                </select>
              </div>
            </div>
          </div>
        </Card>
      )}

      {/* Reset Options */}
      <Card className="p-4">
        <Button
          variant="ghost"
          size="sm"
          onClick={resetToDefaults}
          className="w-full text-xs text-zinc-400 hover:text-zinc-200"
        >
          Reset to Defaults
        </Button>
      </Card>
    </div>
  );
}; 