import React, { useMemo } from 'react';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  LineElement,
  PointElement,
  Title,
  Tooltip,
  Legend,
  Filler,
} from 'chart.js';
import type { Plugin } from 'chart.js';
import { Line } from 'react-chartjs-2';
import type { ChartOptions } from 'chart.js';
import { motion } from 'motion/react';
import { BarChart3, Info } from 'lucide-react';
import { Card } from '../ui/Card';

ChartJS.register(
  CategoryScale,
  LinearScale,
  LineElement,
  PointElement,
  Title,
  Tooltip,
  Legend,
  Filler
);

interface PValueHistogramProps {
  pValues: number[];
  title?: string;
  significanceLevel?: number;
  densityPoints?: number;
}

// Kernel density estimation for smooth distribution curves
const kernelDensityEstimation = (data: number[], bandwidth: number, points: number): { x: number; y: number }[] => {
  if (data.length === 0) return [];
  
  const result: { x: number; y: number }[] = [];
  const step = 1 / (points - 1);
  
  for (let i = 0; i < points; i++) {
    const x = i * step;
    let density = 0;
    
    // Gaussian kernel density estimation
    for (const value of data) {
      const u = (x - value) / bandwidth;
      density += Math.exp(-0.5 * u * u) / Math.sqrt(2 * Math.PI);
    }
    
    density = density / (data.length * bandwidth);
    result.push({ x, y: density });
  }
  
  return result;
};

export const PValueHistogram: React.FC<PValueHistogramProps> = ({
  pValues,
  title = "P-Value Distribution",
  significanceLevel = 0.05,
  densityPoints = 100
}) => {
  const { chartData, significantCount, totalCount /*, maxDensity*/ } = useMemo(() => {
    if (pValues.length === 0) {
      return {
        chartData: { labels: [], datasets: [] },
        significantCount: 0,
        totalCount: 0
        // maxDensity: 0 // Unused
      };
    }

    // Calculate optimal bandwidth for kernel density estimation
    const bandwidth = Math.pow(4 / (3 * pValues.length), 1/5) * Math.sqrt(pValues.reduce((sum, p) => sum + p * p, 0) / pValues.length);
    
    // Get density estimation
    const densityData = kernelDensityEstimation(pValues, bandwidth, densityPoints);
    // const maxDens = Math.max(...densityData.map(d => d.y)); // Unused
    
    // Create labels and data points
    const labels = densityData.map(d => d.x.toFixed(3));
    const densityValues = densityData.map(d => d.y);

    // Find the significance threshold index
    const significanceIndex = Math.floor(significanceLevel * densityPoints);

    // Split data into significant (red) and non-significant (white) portions
    const significantData = densityValues.map((value, index) => 
      index <= significanceIndex ? value : null
    );
    const nonSignificantData = densityValues.map((value, index) => 
      index >= significanceIndex ? value : null
    );

    // Count significant values
    const significant = pValues.filter(p => p < significanceLevel).length;
    const total = pValues.length;

    return {
      chartData: {
        labels,
        datasets: [
          {
            label: 'Significant Region',
            data: significantData,
            backgroundColor: 'rgba(239, 68, 68, 0.3)',
            borderColor: 'rgba(239, 68, 68, 0.9)',
            borderWidth: 3,
            fill: 'origin',
            tension: 0.4,
            pointRadius: 0,
            pointHoverRadius: 0,
            spanGaps: false
          },
          {
            label: 'Non-Significant Region',
            data: nonSignificantData,
            backgroundColor: 'rgba(255, 255, 255, 0.3)',
            borderColor: 'rgba(255, 255, 255, 0.9)',
            borderWidth: 3,
            fill: 'origin',
            tension: 0.4,
            pointRadius: 0,
            pointHoverRadius: 0,
            spanGaps: false
          }
        ]
      },
      significantCount: significant,
      totalCount: total
      // maxDensity: maxDens // Unused
    };
  }, [pValues, significanceLevel, densityPoints]);

  // Custom plugin for significance line
  const significanceLinePlugin: Plugin<'line'> = {
    id: 'significanceLine',
    afterDatasetsDraw: (chart) => {
      const { ctx, chartArea, scales } = chart;
      if (!chartArea || !scales.x) return;

      // Find x position for significance level
      const xPosition = chartArea.left + (chartArea.width * significanceLevel);

      ctx.save();
      ctx.strokeStyle = '#ef4444';
      ctx.lineWidth = 2;
      ctx.setLineDash([5, 5]);
      
      ctx.beginPath();
      ctx.moveTo(xPosition, chartArea.top);
      ctx.lineTo(xPosition, chartArea.bottom);
      ctx.stroke();

      // Label
      ctx.fillStyle = '#ef4444';
      ctx.font = '10px sans-serif';
      ctx.textAlign = 'center';
      ctx.fillText(`α = ${significanceLevel}`, xPosition, chartArea.top - 5);

      ctx.restore();
    }
  };

  const options: ChartOptions<'line'> = {
    responsive: true,
    maintainAspectRatio: false,
    layout: {
      padding: {
        top: 20,
        right: 10,
        bottom: 10,
        left: 10
      }
    },
    plugins: {
      legend: {
        display: false
      },
      title: {
        display: false,
        text: title,
        align: 'start',
        color: '#e4e4e7',
        font: {
          size: 14,
          weight: 'normal' as const,
          family: 'Roboto, sans-serif'
        },
        padding: {
          bottom: 6
        }
      },
      tooltip: {
        backgroundColor: 'rgba(39, 39, 42, 0.95)',
        titleColor: '#e4e4e7',
        bodyColor: '#e4e4e7',
        borderColor: '#71717a',
        borderWidth: 1,
        filter: (tooltipItem) => tooltipItem.raw !== null, // Only show for non-null values
        callbacks: {
          title: (tooltipItems) => {
            const item = tooltipItems[0];
            const pValue = parseFloat(item.label);
            return `P-value: ${pValue.toFixed(3)}`;
          },
          label: (tooltipItem) => {
            const density = tooltipItem.raw as number;
            const isSignificant = tooltipItem.datasetIndex === 0;
            return `Density: ${density.toFixed(4)} ${isSignificant ? '(significant)' : '(not significant)'}`;
          },
          afterBody: () => [
            '',
            'Red curve: statistically significant region (p < α)',
            'White curve: non-significant region (p ≥ α)',
            'Higher peaks = more common p-values'
          ]
        }
      }
    },
    scales: {
      x: {
        type: 'linear',
        min: 0,
        max: 1,
        title: {
          display: true,
          text: 'P-Value',
          color: '#a1a1aa',
          font: { size: 12 }
        },
        ticks: {
          color: '#a1a1aa',
          font: { size: 10 },
          stepSize: 0.1
        },
        grid: {
          color: 'rgba(113, 113, 122, 0.3)'
        }
      },
      y: {
        min: 0,
        title: {
          display: true,
          text: 'Density',
          color: '#a1a1aa',
          font: { size: 12 }
        },
        ticks: {
          color: '#a1a1aa',
          font: { size: 10 }
        },
        grid: {
          color: 'rgba(113, 113, 122, 0.3)'
        }
      }
    },
    elements: {
      line: {
        borderJoinStyle: 'round' as const,
        borderCapStyle: 'round' as const
      }
    },
    animation: {
      duration: 750,
      easing: 'easeOutQuart' as const
    },
    interaction: {
      intersect: false,
      mode: 'index' as const
    }
  };

  return (
    <motion.div
      className="space-y-4"
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
    >
      <Card className="p-4">
        <div className="flex items-center gap-2 mb-4">
          <BarChart3 className="h-5 w-5 text-red-400" />
          <h3 className="text-lg font-semibold text-zinc-200">{title}</h3>
          <div className="ml-auto flex items-center gap-1 text-xs text-zinc-500">
            <Info className="h-3 w-3" />
            Distribution of p-values
          </div>
        </div>
        
        <div className="h-80 mb-4">
          <Line 
            data={chartData} 
            options={options} 
            plugins={[significanceLinePlugin]}
          />
        </div>
        
        {/* Statistical annotations */}
        <div className="flex justify-between text-sm">
          <span className="text-red-400">
            {significantCount} significant values
          </span>
          <span className="text-zinc-400">
            {totalCount - significantCount} false negatives
          </span>
        </div>
        
        <div className="text-center mt-2 text-xs text-zinc-500">
          {totalCount > 0 ? `${((significantCount / totalCount) * 100).toFixed(1)}% of tests were significant` : 'No data'}
        </div>
      </Card>
    </motion.div>
  );
}; 