import React, { useState, useRef, useEffect } from 'react';
import { motion, AnimatePresence } from 'motion/react';
import { PValueHistogram } from './PValueHistogram';
import { ChevronLeft, ChevronRight, Play, Pause } from 'lucide-react';

interface HistogramDataset {
  pValues: number[];
  sampleSize: number;
  label: string;
}

interface HorizontalHistogramCarouselProps {
  datasets: HistogramDataset[];
  significanceLevel: number;
}

export const HorizontalHistogramCarousel: React.FC<HorizontalHistogramCarouselProps> = ({
  datasets,
  significanceLevel
}) => {
  const [currentIndex, setCurrentIndex] = useState(0);
  const [isPlaying, setIsPlaying] = useState(false);
  const intervalRef = useRef<NodeJS.Timeout | null>(null);

  // Auto-play functionality
  useEffect(() => {
    if (isPlaying) {
      intervalRef.current = setInterval(() => {
        setCurrentIndex((prev) => (prev + 1) % datasets.length);
      }, 2000); // Change every 2 seconds
    } else {
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
        intervalRef.current = null;
      }
    }

    return () => {
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
      }
    };
  }, [isPlaying, datasets.length]);

  const goToPrevious = () => {
    setCurrentIndex((prev) => (prev - 1 + datasets.length) % datasets.length);
  };

  const goToNext = () => {
    setCurrentIndex((prev) => (prev + 1) % datasets.length);
  };

  const togglePlayPause = () => {
    setIsPlaying(!isPlaying);
  };

  if (!datasets || datasets.length === 0) {
    return null;
  }

  return (
    <div className="space-y-4">
      {/* Main Carousel Container */}
      <div className="relative overflow-hidden">
        <AnimatePresence mode="wait">
          <motion.div
            key={currentIndex}
            initial={{ x: 300, opacity: 0 }}
            animate={{ x: 0, opacity: 1 }}
            exit={{ x: -300, opacity: 0 }}
            transition={{ 
              type: "spring",
              stiffness: 300,
              damping: 30
            }}
            className="bg-zinc-800 rounded-lg p-6"
          >
            <div className="text-center mb-4">
              <h4 className="text-lg font-semibold text-zinc-100">
                {datasets[currentIndex].label}
              </h4>
              <p className="text-sm text-zinc-400">
                {datasets[currentIndex].pValues.length} simulations
              </p>
            </div>
            
            <PValueHistogram
              pValues={datasets[currentIndex].pValues}
              title=""
              significanceLevel={significanceLevel}
            />
          </motion.div>
        </AnimatePresence>
      </div>

      {/* Navigation Controls */}
      <div className="flex items-center justify-center space-x-4">
        <motion.button
          onClick={goToPrevious}
          className="p-2 bg-zinc-700 hover:bg-zinc-600 rounded-full text-zinc-300 hover:text-white transition-colors"
          whileHover={{ scale: 1.1 }}
          whileTap={{ scale: 0.9 }}
        >
          <ChevronLeft className="h-5 w-5" />
        </motion.button>

        <motion.button
          onClick={togglePlayPause}
          className={`p-2 rounded-full transition-colors ${
            isPlaying 
              ? 'bg-red-600 hover:bg-red-500 text-white' 
              : 'bg-green-600 hover:bg-green-500 text-white'
          }`}
          whileHover={{ scale: 1.1 }}
          whileTap={{ scale: 0.9 }}
        >
          {isPlaying ? <Pause className="h-5 w-5" /> : <Play className="h-5 w-5" />}
        </motion.button>

        <motion.button
          onClick={goToNext}
          className="p-2 bg-zinc-700 hover:bg-zinc-600 rounded-full text-zinc-300 hover:text-white transition-colors"
          whileHover={{ scale: 1.1 }}
          whileTap={{ scale: 0.9 }}
        >
          <ChevronRight className="h-5 w-5" />
        </motion.button>
      </div>

      {/* Time Scroller / Progress Bar */}
      <div className="space-y-2">
        <div className="flex justify-center space-x-2">
          {datasets.map((_, index) => (
            <motion.button
              key={index}
              onClick={() => setCurrentIndex(index)}
              className={`w-3 h-3 rounded-full transition-colors ${
                index === currentIndex 
                  ? 'bg-blue-500' 
                  : 'bg-zinc-600 hover:bg-zinc-500'
              }`}
              whileHover={{ scale: 1.2 }}
              whileTap={{ scale: 0.8 }}
            />
          ))}
        </div>
        
        {/* Scrubber/Timeline */}
        <div className="relative">
          <div className="h-2 bg-zinc-700 rounded-full overflow-hidden">
            <motion.div
              className="h-full bg-blue-500 rounded-full"
              animate={{
                width: `${((currentIndex + 1) / datasets.length) * 100}%`
              }}
              transition={{ type: "spring", stiffness: 400, damping: 30 }}
            />
          </div>
          
          {/* Timeline Labels */}
          <div className="flex justify-between text-xs text-zinc-500 mt-1">
            <span>{datasets[0]?.label}</span>
            <span>{datasets[datasets.length - 1]?.label}</span>
          </div>
        </div>
      </div>

      {/* Educational Context */}
      <div className="bg-blue-900/20 border border-blue-800/30 rounded-lg p-3 text-sm">
        <p className="text-blue-200">
          <strong>Slideshow Mode:</strong> Watch how statistical power changes across different sample sizes. 
          Use play/pause to auto-advance or navigate manually. Notice how larger samples produce more concentrated p-values around significance thresholds.
        </p>
      </div>
    </div>
  );
}; 